import { useDiscountStore } from '../../store/discountStore';
import { useProductStore } from '../../store/productStore';

export function DiscountSettings() {
  const { discounts, updateDiscount } = useDiscountStore();
  const { hddOptions, updateHddPrice, updateHddSku } = useProductStore();

  const handleDiscountChange = (type, field, value) => {
    updateDiscount(type, { [field]: parseFloat(value) });
  };

  const handleHddPriceChange = (size, price) => {
    updateHddPrice(size, parseFloat(price));
  };

  const handleHddSkuChange = (size, sku) => {
    updateHddSku(size, sku);
  };

  return (
    <div className="bg-white shadow overflow-hidden sm:rounded-lg mb-8">
      <div className="px-4 py-5 border-b border-gray-200 sm:px-6">
        <h3 className="text-lg leading-6 font-medium text-gray-900">Discount Settings</h3>
      </div>
      <div className="px-4 py-5 sm:p-6">
        <div className="space-y-6">
          <div>
            <h4 className="text-md font-medium text-gray-900">Bulk Camera Discount</h4>
            <div className="mt-2 grid grid-cols-2 gap-4">
              <div>
                <label className="block text-sm font-medium text-gray-700">Threshold (quantity)</label>
                <input
                  type="number"
                  value={discounts.bulkCamera.threshold}
                  onChange={(e) => handleDiscountChange('bulkCamera', 'threshold', e.target.value)}
                  className="mt-1 block w-full border border-gray-300 rounded-md shadow-sm py-2 px-3 focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm"
                />
              </div>
              <div>
                <label className="block text-sm font-medium text-gray-700">Percentage (%)</label>
                <input
                  type="number"
                  value={discounts.bulkCamera.percentage}
                  onChange={(e) => handleDiscountChange('bulkCamera', 'percentage', e.target.value)}
                  className="mt-1 block w-full border border-gray-300 rounded-md shadow-sm py-2 px-3 focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm"
                />
              </div>
            </div>
          </div>

          <div>
            <h4 className="text-md font-medium text-gray-900">Accessory Bundle Discount</h4>
            <div className="mt-2 grid grid-cols-2 gap-4">
              <div>
                <label className="block text-sm font-medium text-gray-700">Threshold (quantity)</label>
                <input
                  type="number"
                  value={discounts.accessoryBundle.threshold}
                  onChange={(e) => handleDiscountChange('accessoryBundle', 'threshold', e.target.value)}
                  className="mt-1 block w-full border border-gray-300 rounded-md shadow-sm py-2 px-3 focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm"
                />
              </div>
              <div>
                <label className="block text-sm font-medium text-gray-700">Percentage (%)</label>
                <input
                  type="number"
                  value={discounts.accessoryBundle.percentage}
                  onChange={(e) => handleDiscountChange('accessoryBundle', 'percentage', e.target.value)}
                  className="mt-1 block w-full border border-gray-300 rounded-md shadow-sm py-2 px-3 focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm"
                />
              </div>
            </div>
          </div>

          <div>
            <h4 className="text-md font-medium text-gray-900">Complete System Discount</h4>
            <div className="mt-2 grid grid-cols-2 gap-4">
              <div>
                <label className="block text-sm font-medium text-gray-700">Threshold (items)</label>
                <input
                  type="number"
                  value={discounts.completeSystem.threshold}
                  onChange={(e) => handleDiscountChange('completeSystem', 'threshold', e.target.value)}
                  className="mt-1 block w-full border border-gray-300 rounded-md shadow-sm py-2 px-3 focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm"
                />
              </div>
              <div>
                <label className="block text-sm font-medium text-gray-700">Percentage (%)</label>
                <input
                  type="number"
                  value={discounts.completeSystem.percentage}
                  onChange={(e) => handleDiscountChange('completeSystem', 'percentage', e.target.value)}
                  className="mt-1 block w-full border border-gray-300 rounded-md shadow-sm py-2 px-3 focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm"
                />
              </div>
            </div>
          </div>

          <div>
            <h4 className="text-md font-medium text-gray-900">HDD Pricing</h4>
            <div className="mt-2 space-y-4">
              {hddOptions.map(option => (
                <div key={option.size} className="grid grid-cols-3 gap-4">
                  <div className="text-sm font-medium text-gray-700">
                    {option.label}
                  </div>
                  <div>
                    <label className="block text-sm font-medium text-gray-700">Price ($)</label>
                    <input
                      type="number"
                      value={option.price}
                      onChange={(e) => handleHddPriceChange(option.size, e.target.value)}
                      className="mt-1 block w-full rounded-md border-gray-300 shadow-sm focus:border-indigo-500 focus:ring-indigo-500 sm:text-sm"
                      step="0.01"
                      min="0"
                    />
                  </div>
                  <div>
                    <label className="block text-sm font-medium text-gray-700">SKU</label>
                    <input
                      type="text"
                      value={option.sku}
                      onChange={(e) => handleHddSkuChange(option.size, e.target.value)}
                      className="mt-1 block w-full rounded-md border-gray-300 shadow-sm focus:border-indigo-500 focus:ring-indigo-500 sm:text-sm"
                      placeholder="Enter SKU"
                    />
                  </div>
                </div>
              ))}
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}