import { useStore } from '../../store';
import { calculateBundlePrice } from '../../utils/compatibility';
import { calculateDiscounts } from '../../utils/discountCalculator';
import { useThemeStore } from '../../store/themeStore';
import { useProductStore } from '../../store/productStore';

export function CartSidebar() {
  const { selectedNVRs, selectedCameras, accessories, recorderType, removeNVR, removeCamera, removeAccessory } = useStore();
  const { getTheme } = useThemeStore();
  const { hddOptions } = useProductStore();
  const theme = getTheme();
  
  const getHddInfo = (hddSize) => {
    const option = hddOptions.find(hdd => hdd.size === hddSize);
    return option ? { label: option.label, price: option.price } : { label: 'No HDD', price: 0 };
  };

  const calculateTotalPrice = () => {
    const nvrPrice = selectedNVRs.reduce((total, nvr) => {
      const hddPrice = getHddInfo(nvr.hddSize).price;
      return total + ((nvr.price + hddPrice) * nvr.quantity);
    }, 0);

    const cameraPrice = selectedCameras.reduce((total, camera) => 
      total + (camera.price * camera.quantity), 0);

    const accessoryPrice = accessories.reduce((total, acc) => 
      total + (acc.price * acc.quantity), 0);

    return nvrPrice + cameraPrice + accessoryPrice;
  };

  const subtotal = calculateTotalPrice();
  const { discounts, totalDiscount } = calculateDiscounts(selectedNVRs, selectedCameras, accessories);
  const finalTotal = subtotal - totalDiscount;

  const recorderTypeLabel = recorderType === 'digital' ? 'NVR' : 'XVR';

  return (
    <div className={`w-80 h-screen fixed right-0 top-0 bg-${theme.card} dark:bg-gray-800 shadow-lg overflow-y-auto`}>
      <div className="p-4">
        <h2 className={`text-lg font-semibold text-${theme.text} dark:text-gray-100 mb-4`}>Your Bundle</h2>
        
        {recorderType && (
          <div className="mb-2">
            <p className={`text-sm font-medium text-${theme.text} dark:text-gray-100`}>
              System Type: {recorderType === 'digital' ? 'Digital NVR' : 'Analog XVR'}
            </p>
          </div>
        )}
        
        {selectedNVRs.length > 0 && (
          <div className="mb-6">
            <h3 className={`text-sm font-medium text-${theme.text} dark:text-gray-100 mb-2`}>
              {recorderTypeLabel} Systems ({selectedNVRs.length})
            </h3>
            <div className="space-y-3">
              {selectedNVRs.map((nvr) => {
                const hddInfo = getHddInfo(nvr.hddSize);
                const totalPrice = (nvr.price + hddInfo.price) * nvr.quantity;
                
                return (
                  <div key={nvr.id} className="flex items-center space-x-3 relative group">
                    <img src={nvr.images[0]} alt={nvr.name} className="w-12 h-12 object-contain" />
                    <div className="flex-1">
                      <p className={`text-sm font-medium text-${theme.text} dark:text-gray-100`}>
                        {nvr.name}
                      </p>
                      <p className="text-xs text-gray-500 dark:text-gray-400">
                        Quantity: {nvr.quantity}
                      </p>
                      <p className="text-xs text-gray-500 dark:text-gray-400">
                        HDD: {hddInfo.label}
                      </p>
                      <p className={`text-xs font-medium text-${theme.primary}-600 dark:text-${theme.primary}-400`}>
                        ${totalPrice.toFixed(2)}
                      </p>
                    </div>
                    <button 
                      onClick={() => removeNVR(nvr.id)} 
                      className="absolute right-0 top-0 text-gray-400 hover:text-red-500 opacity-0 group-hover:opacity-100 transition-opacity"
                      aria-label="Remove item"
                    >
                      <svg xmlns="http://www.w3.org/2000/svg" className="h-5 w-5" viewBox="0 0 20 20" fill="currentColor">
                        <path fillRule="evenodd" d="M4.293 4.293a1 1 0 011.414 0L10 8.586l4.293-4.293a1 1 0 111.414 1.414L11.414 10l4.293 4.293a1 1 0 01-1.414 1.414L10 11.414l-4.293 4.293a1 1 0 01-1.414-1.414L8.586 10 4.293 5.707a1 1 0 010-1.414z" clipRule="evenodd" />
                      </svg>
                    </button>
                  </div>
                );
              })}
            </div>
          </div>
        )}

        {selectedCameras.length > 0 && (
          <div className="mb-6">
            <h3 className={`text-sm font-medium text-${theme.text} dark:text-gray-100 mb-2`}>
              Cameras ({selectedCameras.length})
            </h3>
            <div className="space-y-3">
              {selectedCameras.map((camera) => (
                <div key={camera.id} className="flex items-center space-x-3 relative group">
                  <img src={camera.images[camera.color][0]} alt={camera.name} className="w-12 h-12 object-contain" />
                  <div className="flex-1">
                    <p className={`text-sm font-medium text-${theme.text} dark:text-gray-100`}>
                      {camera.name}
                    </p>
                    <p className="text-xs text-gray-500 dark:text-gray-400">
                      {camera.color} - Qty: {camera.quantity}
                    </p>
                    <p className={`text-xs font-medium text-${theme.primary}-600 dark:text-${theme.primary}-400`}>
                      ${(camera.price * camera.quantity).toFixed(2)}
                    </p>
                  </div>
                  <button 
                    onClick={() => removeCamera(camera.id)} 
                    className="absolute right-0 top-0 text-gray-400 hover:text-red-500 opacity-0 group-hover:opacity-100 transition-opacity"
                    aria-label="Remove item"
                  >
                    <svg xmlns="http://www.w3.org/2000/svg" className="h-5 w-5" viewBox="0 0 20 20" fill="currentColor">
                      <path fillRule="evenodd" d="M4.293 4.293a1 1 0 011.414 0L10 8.586l4.293-4.293a1 1 0 111.414 1.414L11.414 10l4.293 4.293a1 1 0 01-1.414 1.414L10 11.414l-4.293 4.293a1 1 0 01-1.414-1.414L8.586 10 4.293 5.707a1 1 0 010-1.414z" clipRule="evenodd" />
                    </svg>
                  </button>
                </div>
              ))}
            </div>
          </div>
        )}

        {accessories.length > 0 && (
          <div className="mb-6">
            <h3 className={`text-sm font-medium text-${theme.text} dark:text-gray-100 mb-2`}>
              Accessories ({accessories.length})
            </h3>
            <div className="space-y-3">
              {accessories.map((accessory) => (
                <div key={accessory.id} className="flex items-center space-x-3 relative group">
                  <img src={accessory.images[0]} alt={accessory.name} className="w-12 h-12 object-contain" />
                  <div className="flex-1">
                    <p className={`text-sm font-medium text-${theme.text} dark:text-gray-100`}>
                      {accessory.name}
                    </p>
                    <p className="text-xs text-gray-500 dark:text-gray-400">
                      Quantity: {accessory.quantity}
                    </p>
                    <p className={`text-xs font-medium text-${theme.primary}-600 dark:text-${theme.primary}-400`}>
                      ${(accessory.price * accessory.quantity).toFixed(2)}
                    </p>
                  </div>
                  <button 
                    onClick={() => removeAccessory(accessory.id)} 
                    className="absolute right-0 top-0 text-gray-400 hover:text-red-500 opacity-0 group-hover:opacity-100 transition-opacity"
                    aria-label="Remove item"
                  >
                    <svg xmlns="http://www.w3.org/2000/svg" className="h-5 w-5" viewBox="0 0 20 20" fill="currentColor">
                      <path fillRule="evenodd" d="M4.293 4.293a1 1 0 011.414 0L10 8.586l4.293-4.293a1 1 0 111.414 1.414L11.414 10l4.293 4.293a1 1 0 01-1.414 1.414L10 11.414l-4.293 4.293a1 1 0 01-1.414-1.414L8.586 10 4.293 5.707a1 1 0 010-1.414z" clipRule="evenodd" />
                    </svg>
                  </button>
                </div>
              ))}
            </div>
          </div>
        )}

        <div className="border-t border-gray-200 dark:border-gray-700 pt-4">
          <div className="space-y-2">
            <div className="flex justify-between">
              <span className="text-sm text-gray-500 dark:text-gray-400">Subtotal</span>
              <span className={`text-sm font-medium text-${theme.text} dark:text-gray-100`}>
                ${subtotal.toFixed(2)}
              </span>
            </div>
            {totalDiscount > 0 && (
              <div className="flex justify-between">
                <span className="text-sm text-green-600 dark:text-green-400">Discount</span>
                <span className="text-sm font-medium text-green-600 dark:text-green-400">
                  -${totalDiscount.toFixed(2)}
                </span>
              </div>
            )}
            <div className="flex justify-between">
              <span className={`text-base font-medium text-${theme.text} dark:text-gray-100`}>Total</span>
              <span className={`text-base font-medium text-${theme.text} dark:text-gray-100`}>
                ${finalTotal.toFixed(2)}
              </span>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}