import { useThemeStore } from '../../store/themeStore';

export function ColorSelector({ selectedColor, onColorSelect }) {
  const { getTheme } = useThemeStore();
  const theme = getTheme();

  return (
    <div className="mb-6">
      <label className={`text-sm font-medium text-${theme.text} dark:text-gray-100`}>Camera Color</label>
      <div className="mt-2 flex space-x-4">
        <button
          onClick={() => onColorSelect('black')}
          className={`px-4 py-2 rounded-md ${
            selectedColor === 'black' 
              ? `bg-${theme.primary}-600 text-white` 
              : `bg-${theme.background} dark:bg-gray-700 text-${theme.text} dark:text-gray-100`
          }`}
        >
          Black
        </button>
        <button
          onClick={() => onColorSelect('white')}
          className={`px-4 py-2 rounded-md ${
            selectedColor === 'white' 
              ? `bg-${theme.primary}-600 text-white` 
              : `bg-${theme.background} dark:bg-gray-700 text-${theme.text} dark:text-gray-100`
          }`}
        >
          White
        </button>
      </div>
    </div>
  );
}