import { useStore } from '../../store';
import { useProductStore } from '../../store/productStore';
import { useThemeStore } from '../../store/themeStore';
import { NVRCard } from './NVRCard';

export function NVRSelector({ onNext, onBack }) {
  const { selectedNVRs, addNVR, removeNVR, updateNVRQuantity, updateNVRHdd, recorderType } = useStore();
  const { nvrData } = useProductStore();
  const { getTheme } = useThemeStore();
  const theme = getTheme();

  // Filter NVRs based on the selected recorder type
  const filteredNVRs = nvrData.filter(nvr => nvr.type === recorderType);

  const handleNVRSelect = (nvr) => {
    const isSelected = selectedNVRs.some(n => n.id === nvr.id);
    if (isSelected) {
      removeNVR(nvr.id);
    } else {
      // Ensure nvr has an images array, even if empty
      const nvrWithImages = {
        ...nvr,
        images: nvr.images && Array.isArray(nvr.images) && nvr.images.length > 0 
          ? nvr.images 
          : ['https://via.placeholder.com/300x200?text=No+Image']
      };
      addNVR(nvrWithImages, 1, 0); // Default to no HDD
    }
  };

  const handleQuantityChange = (nvrId, quantity) => {
    updateNVRQuantity(nvrId, quantity);
  };

  const handleHddChange = (nvrId, hddSize) => {
    updateNVRHdd(nvrId, hddSize);
  };

  const getNVRQuantity = (nvrId) => {
    const nvr = selectedNVRs.find(n => n.id === nvrId);
    return nvr ? nvr.quantity : 1;
  };

  const getNVRHddSize = (nvrId) => {
    const nvr = selectedNVRs.find(n => n.id === nvrId);
    return nvr ? nvr.hddSize : 0;
  };

  const recorderTypeLabel = recorderType === 'digital' ? 'NVR' : 'XVR';

  return (
    <div className="p-6">
      <h2 className={`text-xl font-semibold text-${theme.text} dark:text-gray-100 mb-6`}>
        Select {recorderTypeLabel} System(s)
      </h2>
      
      {filteredNVRs.length === 0 ? (
        <div className="text-center py-8">
          <p className={`text-lg text-${theme.text} dark:text-gray-100`}>
            No {recorderTypeLabel} systems available. Please contact support.
          </p>
        </div>
      ) : (
        <div className="grid grid-cols-1 gap-6 sm:grid-cols-2 lg:grid-cols-3">
          {filteredNVRs.map((nvr) => (
            <NVRCard
              key={nvr.id}
              nvr={{
                ...nvr,
                hddSize: getNVRHddSize(nvr.id)
              }}
              isSelected={selectedNVRs.some(n => n.id === nvr.id)}
              onSelect={handleNVRSelect}
              quantity={getNVRQuantity(nvr.id)}
              onQuantityChange={(quantity) => handleQuantityChange(nvr.id, quantity)}
              onHddChange={(hddSize) => handleHddChange(nvr.id, hddSize)}
            />
          ))}
        </div>
      )}
      
      <div className="mt-8 flex justify-between">
        {/* Only show back button if onBack is provided */}
        {onBack && (
          <button
            onClick={onBack}
            className={`px-4 py-2 border rounded-md hover:bg-${theme.background} dark:hover:bg-gray-700 text-${theme.text} dark:text-gray-100`}
          >
            Back
          </button>
        )}
        
        {/* If there's no back button, use justify-end instead of justify-between */}
        <div className={onBack ? '' : 'ml-auto'}>
          <button
            onClick={onNext}
            disabled={selectedNVRs.length === 0}
            className={`px-4 py-2 bg-${theme.primary}-600 text-white rounded-md 
              hover:bg-${theme.primary}-700 disabled:bg-gray-300 disabled:cursor-not-allowed`}
          >
            Continue
          </button>
        </div>
      </div>
    </div>
  );
}