import { useState } from 'react';
import { useStore } from '../../store';
import { calculateBundlePrice } from '../../utils/compatibility';
import { generateOrder, exportOrder } from '../../utils/orderGenerator';
import { useThemeStore } from '../../store/themeStore';
import { calculateDiscounts } from '../../utils/discountCalculator';
import { useProductStore } from '../../store/productStore';

export function BundleSummary({ onBack }) {
  const [isAddingToCart, setIsAddingToCart] = useState(false);
  const [cartSuccess, setCartSuccess] = useState(false);
  const [cartError, setCartError] = useState('');
  
  const { selectedNVRs, selectedCameras, accessories, recorderType } = useStore();
  const { getWooCommerceProductId, getHddWooCommerceProductId } = useProductStore();
  
  const subtotal = calculateBundlePrice(selectedNVRs, selectedCameras, accessories);
  const { discounts, totalDiscount } = calculateDiscounts(selectedNVRs, selectedCameras, accessories);
  const finalTotal = subtotal - totalDiscount;
  const { getTheme } = useThemeStore();
  const theme = getTheme();

  const handleCheckout = () => {
    const orderContent = generateOrder(selectedNVRs, selectedCameras, accessories, recorderType);
    exportOrder(orderContent);
  };

  const handleAddToCart = async () => {
    try {
      setIsAddingToCart(true);
      setCartSuccess(false);
      setCartError('');
      
      // Prepare the bundle items for the cart
      const bundleItems = [];
      
      // Add NVRs to cart
      selectedNVRs.forEach(nvr => {
        const nvrProductId = getWooCommerceProductId(nvr.sku);
        if (nvrProductId) {
          bundleItems.push({
            product_id: nvrProductId,
            quantity: nvr.quantity || 1
          });
          
          // Add HDD if selected
          if (nvr.hddSize && nvr.hddSize > 0) {
            const hddProductId = getHddWooCommerceProductId(nvr.hddSize);
            if (hddProductId) {
              bundleItems.push({
                product_id: hddProductId,
                quantity: 1
              });
            }
          }
        }
      });
      
      // Add cameras to cart
      selectedCameras.forEach(camera => {
        // Handle color variants if needed
        const cameraProductId = getWooCommerceProductId(camera.sku);
        if (cameraProductId) {
          bundleItems.push({
            product_id: cameraProductId,
            quantity: camera.quantity || 1
          });
        }
      });
      
      // Add accessories to cart
      accessories.forEach(accessory => {
        const accessoryProductId = getWooCommerceProductId(accessory.sku);
        if (accessoryProductId) {
          bundleItems.push({
            product_id: accessoryProductId,
            quantity: accessory.quantity || 1
          });
        }
      });
      
      // Call the WooCommerce API to add items to cart
      if (bundleItems.length === 0) {
        setCartError('No valid WooCommerce products found for your bundle items');
        setIsAddingToCart(false);
        return;
      }
      
      const response = await fetch('/wp-json/nvr-bundle/v1/add-to-cart', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json'
        },
        body: JSON.stringify({
          items: bundleItems
        })
      });
      
      if (!response.ok) {
        throw new Error(`API error: ${response.status}`);
      }
      
      const result = await response.json();
      
      if (result.success) {
        setCartSuccess(true);
      } else {
        setCartError(result.message || 'Failed to add bundle to cart');
      }
    } catch (error) {
      console.error('Error adding bundle to cart:', error);
      setCartError(`Error adding bundle to cart: ${error.message}`);
    } finally {
      setIsAddingToCart(false);
    }
  };

  const recorderTypeLabel = recorderType === 'digital' ? 'NVR' : 'XVR';

  return (
    <div className="p-6">
      <h2 className={`text-xl font-semibold text-${theme.text} dark:text-gray-100 mb-6`}>Bundle Summary</h2>
      
      <div className="space-y-6">
        {/* NVRs Section */}
        <div className={`bg-${theme.background} dark:bg-gray-700 p-4 rounded-lg`}>
          <h3 className={`text-lg font-medium text-${theme.text} dark:text-gray-100 mb-2`}>
            {recorderTypeLabel} Systems ({selectedNVRs.length})
          </h3>
          <div className="space-y-4">
            {selectedNVRs.map((nvr) => (
              <div key={nvr.id} className="flex items-center space-x-4">
                {nvr.images && nvr.images[0] && (
                  <img src={nvr.images[0]} alt={nvr.name} className="w-24 h-24 object-contain" />
                )}
                <div>
                  <p className={`font-medium text-${theme.text} dark:text-gray-100`}>{nvr.name}</p>
                  <p className="text-sm text-gray-500 dark:text-gray-400">SKU: {nvr.sku}</p>
                  <p className="text-sm text-gray-500 dark:text-gray-400">{nvr.channels} Channels</p>
                  {nvr.hddSize > 0 && (
                    <p className="text-sm text-gray-500 dark:text-gray-400">{nvr.hddSize}TB Hard Drive</p>
                  )}
                  <p className={`text-sm font-medium text-${theme.primary}-600 dark:text-${theme.primary}-400`}>
                    ${(nvr.price * (nvr.quantity || 1)).toFixed(2)}
                  </p>
                </div>
              </div>
            ))}
          </div>
        </div>

        {/* Cameras Section */}
        <div className={`bg-${theme.background} dark:bg-gray-700 p-4 rounded-lg`}>
          <h3 className={`text-lg font-medium text-${theme.text} dark:text-gray-100 mb-2`}>
            Cameras ({selectedCameras.length})
          </h3>
          <div className="grid grid-cols-1 gap-4 sm:grid-cols-2">
            {selectedCameras.map((camera) => (
              <div key={camera.id} className="flex items-center space-x-4">
                {camera.images && camera.color && camera.images[camera.color] && camera.images[camera.color][0] && (
                  <img src={camera.images[camera.color][0]} alt={camera.name} className="w-16 h-16 object-contain" />
                )}
                <div>
                  <p className={`font-medium text-${theme.text} dark:text-gray-100`}>{camera.name}</p>
                  <p className="text-sm text-gray-500 dark:text-gray-400">{camera.color}</p>
                  <p className="text-sm text-gray-500 dark:text-gray-400">Quantity: {camera.quantity || 1}</p>
                  <p className={`text-sm font-medium text-${theme.primary}-600 dark:text-${theme.primary}-400`}>
                    ${(camera.price * (camera.quantity || 1)).toFixed(2)}
                  </p>
                </div>
              </div>
            ))}
          </div>
        </div>

        {/* Accessories Section */}
        {accessories.length > 0 && (
          <div className={`bg-${theme.background} dark:bg-gray-700 p-4 rounded-lg`}>
            <h3 className={`text-lg font-medium text-${theme.text} dark:text-gray-100 mb-2`}>Accessories</h3>
            <div className="grid grid-cols-1 gap-4 sm:grid-cols-2">
              {accessories.map((accessory) => (
                <div key={accessory.id} className="flex items-center space-x-4">
                  {accessory.images && accessory.images[0] && (
                    <img src={accessory.images[0]} alt={accessory.name} className="w-16 h-16 object-contain" />
                  )}
                  <div>
                    <p className={`font-medium text-${theme.text} dark:text-gray-100`}>{accessory.name}</p>
                    <p className="text-sm text-gray-500 dark:text-gray-400">Quantity: {accessory.quantity || 1}</p>
                    <p className={`text-sm font-medium text-${theme.primary}-600 dark:text-${theme.primary}-400`}>
                      ${(accessory.price * (accessory.quantity || 1)).toFixed(2)}
                    </p>
                  </div>
                </div>
              ))}
            </div>
          </div>
        )}

        {/* Discounts Section */}
        <div className={`bg-${theme.background} dark:bg-gray-700 p-4 rounded-lg`}>
          <h3 className={`text-lg font-medium text-${theme.text} dark:text-gray-100 mb-4`}>Applied Discounts</h3>
          {discounts.length > 0 ? (
            <div className="space-y-2">
              {discounts.map((discount, index) => (
                <div key={index} className="flex justify-between items-center">
                  <p className="text-sm text-gray-500 dark:text-gray-400">{discount.description}</p>
                  <p className={`text-sm font-medium text-green-600 dark:text-green-400`}>
                    -${discount.amount.toFixed(2)}
                  </p>
                </div>
              ))}
            </div>
          ) : (
            <p className="text-sm text-gray-500 dark:text-gray-400">No discounts available for this bundle</p>
          )}
        </div>

        {/* Success/Error Messages */}
        {cartSuccess && (
          <div className="p-4 bg-green-100 border border-green-300 rounded-lg">
            <div className="flex">
              <div className="flex-shrink-0">
                <svg className="h-5 w-5 text-green-500" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 20" fill="currentColor">
                  <path fillRule="evenodd" d="M10 18a8 8 0 100-16 8 8 0 000 16zm3.707-9.293a1 1 0 00-1.414-1.414L9 10.586 7.707 9.293a1 1 0 00-1.414 1.414l2 2a1 1 0 001.414 0l4-4z" clipRule="evenodd" />
                </svg>
              </div>
              <div className="ml-3">
                <p className="text-sm font-medium text-green-800">Bundle successfully added to cart!</p>
                <div className="mt-2">
                  <a href="/cart" className={`inline-block text-sm font-medium text-${theme.primary}-600 hover:text-${theme.primary}-500`}>
                    View Cart
                  </a>
                </div>
              </div>
            </div>
          </div>
        )}

        {cartError && (
          <div className="p-4 bg-red-100 border border-red-300 rounded-lg">
            <div className="flex">
              <div className="flex-shrink-0">
                <svg className="h-5 w-5 text-red-500" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 20" fill="currentColor">
                  <path fillRule="evenodd" d="M10 18a8 8 0 100-16 8 8 0 000 16zM8.707 7.293a1 1 0 00-1.414 1.414L8.586 10l-1.293 1.293a1 1 0 101.414 1.414L10 11.414l1.293 1.293a1 1 0 001.414-1.414L11.414 10l1.293-1.293a1 1 0 00-1.414-1.414L10 8.586 8.707 7.293z" clipRule="evenodd" />
                </svg>
              </div>
              <div className="ml-3">
                <p className="text-sm font-medium text-red-800">Error adding bundle to cart</p>
                <p className="text-sm text-red-700 mt-1">{cartError}</p>
              </div>
            </div>
          </div>
        )}

        {/* Total Section */}
        <div className="mt-8 flex justify-between items-center">
          <button
            onClick={onBack}
            className={`px-4 py-2 border rounded-md hover:bg-${theme.background} dark:hover:bg-gray-700 text-${theme.text} dark:text-gray-100`}
          >
            Back
          </button>
          <div className="space-y-4 text-right">
            <div className="space-y-1">
              <p className="text-sm text-gray-500 dark:text-gray-400">Subtotal: ${subtotal.toFixed(2)}</p>
              {totalDiscount > 0 && (
                <p className="text-sm text-green-600 dark:text-green-400">
                  Total Savings: -${totalDiscount.toFixed(2)}
                </p>
              )}
              <p className={`text-2xl font-bold text-${theme.text} dark:text-gray-100`}>
                Final Total: ${finalTotal.toFixed(2)}
              </p>
            </div>
            <div className="flex space-x-3">
              <button
                onClick={handleCheckout}
                className={`px-4 py-2 border border-gray-300 rounded-md text-${theme.text} dark:text-gray-100 hover:bg-${theme.background} dark:hover:bg-gray-700`}
              >
                Generate Order
              </button>
              <button
                onClick={handleAddToCart}
                disabled={isAddingToCart || selectedNVRs.length === 0}
                className={`px-6 py-2 bg-${theme.primary}-600 hover:bg-${theme.primary}-700 text-white rounded-md font-medium ${
                  isAddingToCart || selectedNVRs.length === 0 ? 'opacity-50 cursor-not-allowed' : ''
                }`}
              >
                {isAddingToCart ? 'Adding...' : 'Add to Cart'}
              </button>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}