// src/components/admin/Dashboard.jsx
import { useState } from 'react';
import { ProductList } from './ProductList';
import { AddProductForm } from './AddProductForm';
import { DiscountSettings } from './DiscountSettings';
import { ProductImport } from './ProductImport';
import { useProductStore } from '../../store/productStore';
import { useThemeStore } from '../../store/themeStore';

export function Dashboard() {
  const [activeTab, setActiveTab] = useState('products');
  const { nvrData, cameraData, accessoryData } = useProductStore();
  const { getTheme } = useThemeStore();
  const theme = getTheme();
  
  const tabs = [
    { id: 'products', name: 'Products' },
    { id: 'add', name: 'Add Product' },
    { id: 'discounts', name: 'Discount Settings' },
    { id: 'import', name: 'WooCommerce Import' }
  ];
  
  return (
    <div className={`bg-${theme.background} dark:bg-gray-900 min-h-screen p-4`}>
      <div className="max-w-7xl mx-auto">
        <div className={`bg-${theme.card} dark:bg-gray-800 shadow-sm rounded-lg overflow-hidden mb-8`}>
          <div className="sm:hidden">
            <label htmlFor="tabs" className="sr-only">Select a tab</label>
            <select
              id="tabs"
              name="tabs"
              className={`block w-full focus:ring-${theme.primary}-500 focus:border-${theme.primary}-500 border-gray-300 rounded-md`}
              value={activeTab}
              onChange={(e) => setActiveTab(e.target.value)}
            >
              {tabs.map((tab) => (
                <option key={tab.id} value={tab.id}>{tab.name}</option>
              ))}
            </select>
          </div>
          <div className="hidden sm:block">
            <div className="border-b border-gray-200 dark:border-gray-700">
              <nav className="-mb-px flex" aria-label="Tabs">
                {tabs.map((tab) => (
                  <button
                    key={tab.id}
                    onClick={() => setActiveTab(tab.id)}
                    className={`
                      w-1/4 py-4 px-1 text-center border-b-2 font-medium text-sm
                      ${activeTab === tab.id
                        ? `border-${theme.primary}-500 text-${theme.primary}-600 dark:text-${theme.primary}-400`
                        : `border-transparent text-${theme.text} dark:text-gray-400 hover:text-${theme.primary}-500 hover:border-${theme.primary}-300`}
                    `}
                  >
                    {tab.name}
                  </button>
                ))}
              </nav>
            </div>
          </div>
          
          {activeTab === 'products' && (
            <div className="p-4 divide-y divide-gray-200 dark:divide-gray-700">
              <ProductList title="NVR Systems" products={nvrData} category="nvr" />
              <div className="pt-6">
                <ProductList title="Cameras" products={cameraData} category="camera" />
              </div>
              <div className="pt-6">
                <ProductList title="Accessories" products={accessoryData} category="accessory" />
              </div>
            </div>
          )}
          
          {activeTab === 'add' && (
            <div className="p-4">
              <AddProductForm />
            </div>
          )}
          
          {activeTab === 'discounts' && (
            <div className="p-4">
              <DiscountSettings />
            </div>
          )}
          
          {activeTab === 'import' && (
            <div className="p-4">
              <ProductImport />
            </div>
          )}
        </div>
      </div>
    </div>
  );
}