import { useState } from 'react';
import { ImageControls } from './ImageControls';
import { ImageDots } from './ImageDots';

export function ImageGallery({ images, className = "", onImageClick }) {
  const [currentImageIndex, setCurrentImageIndex] = useState(0);
  
  // Handle empty or undefined images
  const imageArray = Array.isArray(images) && images.length > 0 
    ? images.filter(img => img && img.trim() !== '') 
    : ['https://via.placeholder.com/300x200?text=No+Image'];

  const nextImage = (e) => {
    e.stopPropagation();
    setCurrentImageIndex((prev) => (prev + 1) % imageArray.length);
  };

  const previousImage = (e) => {
    e.stopPropagation();
    setCurrentImageIndex((prev) => (prev - 1 + imageArray.length) % imageArray.length);
  };

  const selectImage = (index) => {
    setCurrentImageIndex(index);
  };

  const handleImageClick = (e) => {
    e.stopPropagation();
    if (onImageClick) {
      onImageClick(imageArray[currentImageIndex]);
    }
  };

  const handleImageError = (e) => {
    e.target.src = 'https://via.placeholder.com/300x200?text=Image+Error';
  };

  return (
    <div className={`relative ${className}`}>
      <div className="relative">
        <img
          src={imageArray[currentImageIndex]}
          alt="Product"
          className="w-full h-48 object-contain"
          onClick={handleImageClick}
          onError={handleImageError}
        />
        {imageArray.length > 1 && (
          <div className="absolute inset-0 flex items-center justify-between">
            <ImageControls 
              onPrevious={previousImage}
              onNext={nextImage}
              showControls={imageArray.length > 1}
            />
          </div>
        )}
      </div>
      <ImageDots 
        count={imageArray.length}
        currentIndex={currentImageIndex}
        onSelect={selectImage}
      />
    </div>
  );
}