import { useState } from 'react';
import { useProductStore } from '../../store/productStore';
import { ImageUploadField } from './ImageUploadField';
import { ImageGallery } from '../common/ImageGallery';

export function ProductList({ title, products, category }) {
  const [editingId, setEditingId] = useState(null);
  const { updateProduct, deleteProduct, hddOptions, updateHddPrice } = useProductStore();

  const handleEdit = (product) => {
    setEditingId(product.id);
  };

  const handleSave = (product, updates) => {
    updateProduct(category, product.id, updates);
    setEditingId(null);
  };

  const handleDelete = (id) => {
    if (window.confirm('Are you sure you want to delete this item?')) {
      deleteProduct(category, id);
    }
  };

  const handleHddPriceChange = (size, price) => {
    updateHddPrice(size, parseFloat(price));
  };

  return (
    <div className="bg-white shadow overflow-hidden sm:rounded-md">
      <div className="px-4 py-5 border-b border-gray-200 sm:px-6">
        <h3 className="text-lg leading-6 font-medium text-gray-900">{title}</h3>
      </div>

      {category === 'nvr' && (
        <div className="px-4 py-5 border-b border-gray-200">
          <h4 className="text-md font-medium text-gray-900 mb-4">HDD Pricing</h4>
          <div className="grid grid-cols-2 md:grid-cols-3 lg:grid-cols-5 gap-4">
            {hddOptions.map(option => (
              <div key={option.size} className="space-y-1">
                <label className="block text-sm font-medium text-gray-700">
                  {option.label}
                </label>
                <div className="flex items-center">
                  <span className="text-gray-500 mr-1">$</span>
                  <input
                    type="number"
                    value={option.price}
                    onChange={(e) => handleHddPriceChange(option.size, e.target.value)}
                    className="block w-full rounded-md border-gray-300 shadow-sm focus:border-indigo-500 focus:ring-indigo-500 sm:text-sm"
                    step="0.01"
                    min="0"
                  />
                </div>
              </div>
            ))}
          </div>
        </div>
      )}

      <ul className="divide-y divide-gray-200">
        {products.map((product) => (
          <li key={product.id} className="px-4 py-4">
            {editingId === product.id ? (
              <EditForm
                product={product}
                category={category}
                onSave={handleSave}
                onCancel={() => setEditingId(null)}
              />
            ) : (
              <div className="flex justify-between items-start">
                <div className="flex items-start space-x-4">
                  <div className="w-32">
                    {category === 'camera' ? (
                      <ImageGallery 
                        images={product.images?.black} 
                        className="w-32 h-32"
                      />
                    ) : (
                      <ImageGallery 
                        images={product.images} 
                        className="w-32 h-32"
                      />
                    )}
                  </div>
                  <div>
                    <h4 className="text-lg font-medium">{product.name}</h4>
                    <p className="text-sm text-gray-500">SKU: {product.sku}</p>
                    <p className="text-sm text-gray-500">Price: ${product.price}</p>
                    <p className="text-sm text-gray-500">
                      Type: {product.type === 'digital' ? ' Digital' : product.type === 'analog' ? 'Analog' : 'Both'}
                    </p>
                    {category === 'nvr' && (
                      <p className="text-sm text-gray-500">Channels: {product.channels}</p>
                    )}
                    {category === 'camera' && (
                      <p className="text-sm text-gray-500">Resolution: {product.resolution}</p>
                    )}
                  </div>
                </div>
                <div className="space-x-2">
                  <button
                    onClick={() => handleEdit(product)}
                    className="px-3 py-1 border border-gray-300 rounded-md text-sm font-medium text-gray-700 hover:bg-gray-50"
                  >
                    Edit
                  </button>
                  <button
                    onClick={() => handleDelete(product.id)}
                    className="px-3 py-1 border border-red-300 rounded-md text-sm font-medium text-red-700 hover:bg-red-50"
                  >
                    Delete
                  </button>
                </div>
              </div>
            )}
          </li>
        ))}
      </ul>
    </div>
  );
}

function EditForm({ product, category, onSave, onCancel }) {
  const [formData, setFormData] = useState({
    ...product,
    currentColor: 'black'
  });
  const [errors, setErrors] = useState({});

  const handleChange = (e) => {
    const { name, value } = e.target;
    
    // Clear error when field is edited
    if (errors[name]) {
      setErrors({...errors, [name]: null});
    }
    
    setFormData(prev => ({
      ...prev,
      [name]: (name === 'price' || name === 'channels') 
        ? (value === '' ? '' : parseFloat(value)) 
        : value
    }));
  };

  const handleImageChange = (images) => {
    if (category === 'camera') {
      // Ensure images object exists
      const currentImages = formData.images || { black: [], white: [] };
      
      setFormData(prev => ({
        ...prev,
        images: {
          ...currentImages,
          [prev.currentColor]: images
        }
      }));
    } else {
      setFormData(prev => ({
        ...prev,
        images
      }));
    }
  };

  const handleColorChange = (color) => {
    setFormData(prev => ({
      ...prev,
      currentColor: color
    }));
  };

  const validateForm = () => {
    const newErrors = {};
    
    if (!formData.name?.trim()) {
      newErrors.name = "Name is required";
    }
    
    if (!formData.sku?.trim()) {
      newErrors.sku = "SKU is required";
    }
    
    if (formData.price === '' || isNaN(formData.price) || formData.price < 0) {
      newErrors.price = "Price must be a positive number";
    }
    
    if (category === 'nvr' && (formData.channels === '' || isNaN(formData.channels) || formData.channels < 1)) {
      newErrors.channels = "Channels must be a positive number";
    }
    
    setErrors(newErrors);
    return Object.keys(newErrors).length === 0;
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    
    if (validateForm()) {
      // Format data for submission
      const formattedData = {
        ...formData,
        price: parseFloat(formData.price) || 0,
        ...(category === 'nvr' && { channels: parseInt(formData.channels) || 0 })
      };
      
      onSave(product, formattedData);
    }
  };

  return (
    <form onSubmit={handleSubmit} className="space-y-4">
      <div className="grid grid-cols-2 gap-4">
        <div className="space-y-4">
          <div>
            <label className="block text-sm font-medium text-gray-700">Name</label>
            <input
              type="text"
              name="name"
              value={formData.name}
              onChange={handleChange}
              className="mt-1 block w-full border border-gray-300 rounded-md shadow-sm py-2 px-3 focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm"
            />
          </div>
          <div>
            <label className="block text-sm font-medium text-gray-700">SKU</label>
            <input
              type="text"
              name="sku"
              value={formData.sku}
              onChange={handleChange}
              className="mt-1 block w-full border border-gray-300 rounded-md shadow-sm py-2 px-3 focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm"
            />
          </div>
          <div>
            <label className="block text-sm font-medium text-gray-700">Price</label>
            <input
              type="number"
              name="price"
              value={formData.price}
              onChange={handleChange}
              step="0.01"
              className="mt-1 block w-full border border-gray-300 rounded-md shadow-sm py-2 px-3 focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm"
            />
          </div>
          <div>
            <label className="block text-sm font-medium text-gray-700">Type</label>
            <select
              name="type"
              value={formData.type}
              onChange={handleChange}
              className="mt-1 block w-full border border-gray-300 rounded-md shadow-sm py-2 px-3 focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm"
            >
              {category === 'nvr' ? (
                <>
                  <option value="digital">Digital (NVR)</option>
                  <option value="analog">Analog (XVR)</option>
                </>
              ) : category === 'camera' ? (
                <>
                  <option value="digital">Digital (IP Camera)</option>
                  <option value="analog">Analog Camera</option>
                </>
              ) : (
                <>
                  <option value="digital">Digital Only</option>
                  <option value="analog">Analog Only</option>
                  <option value="both">Compatible with Both</option>
                </>
              )}
            </select>
          </div>
          {category === 'nvr' && (
            <div>
              <label className="block text-sm font-medium text-gray-700">Channels</label>
              <input
                type="number"
                name="channels"
                value={formData.channels}
                onChange={handleChange}
                className="mt-1 block w-full border border-gray-300 rounded-md shadow-sm py-2 px-3 focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm"
              />
            </div>
          )}
          {category === 'camera' && (
            <div>
              <label className="block text-sm font-medium text-gray-700">Resolution</label>
              <input
                type="text"
                name="resolution"
                value={formData.resolution}
                onChange={handleChange}
                className="mt-1 block w-full border border-gray-300 rounded-md shadow-sm py-2 px-3 focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm"
              />
            </div>
          )}
        </div>
        
        <div className="space-y-4">
          {category === 'camera' ? (
            <div className="space-y-4">
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">Color</label>
                <div className="flex space-x-4">
                  <button
                    type="button"
                    onClick={() => handleColorChange('black')}
                    className={`px-4 py-2 rounded-md ${
                      formData.currentColor === 'black' 
                        ? 'bg-gray-900 text-white' 
                        : 'bg-gray-100 text-gray-900'
                    }`}
                  >
                    Black
                  </button>
                  <button
                    type="button"
                    onClick={() => handleColorChange('white')}
                    className={`px-4 py-2 rounded-md ${
                      formData.currentColor === 'white' 
                        ? 'bg-gray-100 text-gray-900 border-2 border-gray-900' 
                        : 'bg-gray-100 text-gray-900'
                    }`}
                  >
                    White
                  </button>
                </div>
              </div>
              <ImageUploadField 
                images={formData.images[formData.currentColor]} 
                onChange={handleImageChange}
              />
            </div>
          ) : (
            <ImageUploadField 
              images={formData.images} 
              onChange={handleImageChange}
            />
          )}
        </div>
      </div>

      <div className="flex justify-end space-x-2 mt-4">
        <button
          type="button"
          onClick={onCancel}
          className="px-4 py-2 border border-gray-300 rounded-md text-sm font-medium text-gray-700 hover:bg-gray-50"
        >
          Cancel
        </button>
        <button
          type="submit"
          className="px-4 py-2 border border-transparent text-sm font-medium rounded-md text-white bg-indigo-600 hover:bg-indigo-700"
        >
          Save
        </button>
      </div>
    </form>
  );
}