// src/store/productStore.js
import create from 'zustand';
import { persist } from 'zustand/middleware';
import { nvrData as defaultNvrData, cameraData as defaultCameraData, accessoryData as defaultAccessoryData } from '../data/nvrData';

export const useProductStore = create(
  persist(
    (set, get) => ({
      nvrData: defaultNvrData,
      cameraData: defaultCameraData,
      accessoryData: defaultAccessoryData,
      skuToProductIdMap: {}, // Map of SKUs to WooCommerce product IDs
      compatibilityMap: {}, // Map of NVR SKUs to compatible camera SKUs
      hddOptions: [
        { size: 1, label: '1TB', price: 49.99, sku: '' },
        { size: 2, label: '2TB', price: 79.99, sku: '' },
        { size: 4, label: '4TB', price: 129.99, sku: '' },
        { size: 6, label: '6TB', price: 189.99, sku: '' },
        { size: 8, label: '8TB', price: 249.99, sku: '' }
      ],
      
      // Update a single product
      updateProduct: (category, id, updates) => {
        set((state) => {
          const dataKey = `${category}Data`;
          const items = state[dataKey].map((item) =>
            item.id === id ? { ...item, ...updates } : item
          );
          return { [dataKey]: items };
        });
      },
      
      // Set all products for a category
      setProducts: (category, products) => {
        set((state) => {
          const dataKey = `${category}Data`;
          return { [dataKey]: products };
        });
      },
      
      // Update the compatibility map
      setCompatibilityMap: (compatibilityMap) => {
        set({ compatibilityMap });
      },
      
      // Update the SKU to product ID map
      setSkuToProductIdMap: (skuToProductIdMap) => {
        set({ skuToProductIdMap });
      },
      
      // Update HDD price
      updateHddPrice: (size, newPrice) => {
        set((state) => ({
          hddOptions: state.hddOptions.map(hdd =>
            hdd.size === size ? { ...hdd, price: newPrice } : hdd
          )
        }));
      },
      
      // Update HDD SKU
      updateHddSku: (size, sku) => {
        set((state) => ({
          hddOptions: state.hddOptions.map(hdd =>
            hdd.size === size ? { ...hdd, sku } : hdd
          )
        }));
      },
      
      // Get product by ID
      getProductById: (category, id) => {
        const state = get();
        const dataKey = `${category}Data`;
        return state[dataKey].find(item => item.id === id);
      },
      
      // Get product by SKU
      getProductBySku: (category, sku) => {
        const state = get();
        const dataKey = `${category}Data`;
        return state[dataKey].find(item => item.sku === sku);
      },
      
      // Get WooCommerce product ID by SKU
      getWooCommerceProductId: (sku) => {
        const state = get();
        return state.skuToProductIdMap[sku] || 0;
      },
      
      // Get HDD WooCommerce product ID by size
      getHddWooCommerceProductId: (size) => {
        const state = get();
        const hdd = state.hddOptions.find(h => h.size === size);
        if (hdd && hdd.sku) {
          return state.skuToProductIdMap[hdd.sku] || 0;
        }
        return 0;
      },
      
      // Get compatible cameras for an NVR
      getCompatibleCameras: (nvrSku) => {
        const state = get();
        if (state.compatibilityMap[nvrSku]) {
          return state.compatibilityMap[nvrSku];
        }
        
        // Fallback to compatibility based on type (digital/analog)
        const nvr = state.nvrData.find(n => n.sku === nvrSku);
        if (!nvr) return [];
        
        return state.cameraData
          .filter(camera => camera.type === nvr.type)
          .map(camera => camera.sku);
      },
      
      // Add a new product
      addProduct: (category, product) => {
        set((state) => {
          const dataKey = `${category}Data`;
          return { [dataKey]: [...state[dataKey], product] };
        });
      },
      
      // Delete a product
      deleteProduct: (category, id) => {
        set((state) => {
          const dataKey = `${category}Data`;
          return { [dataKey]: state[dataKey].filter((item) => item.id !== id) };
        });
      },
      
      // Reset to defaults
      resetToDefaults: () => {
        set({
          nvrData: defaultNvrData,
          cameraData: defaultCameraData,
          accessoryData: defaultAccessoryData,
          skuToProductIdMap: {},
          compatibilityMap: {}
        });
      }
    }),
    {
      name: 'product-storage',
      version: 1,
      migrate: (persistedState, version) => {
        if (version === 0) {
          return {
            ...persistedState,
            nvrData: defaultNvrData,
            cameraData: defaultCameraData,
            accessoryData: defaultAccessoryData,
            skuToProductIdMap: {},
            compatibilityMap: {}
          };
        }
        return persistedState;
      }
    }
  )
);