import { useState } from 'react';
import { useStore } from '../../store';
import { useProductStore } from '../../store/productStore';
import { getCompatibleCameras } from '../../utils/compatibility';
import { useThemeStore } from '../../store/themeStore';
import { CameraCard } from './CameraCard';
import { ColorSelector } from './ColorSelector';

export function CameraSelector({ onNext, onBack, totalChannels }) {
  const { selectedNVRs, selectedCameras, addCamera, removeCamera, updateCameraQuantity, recorderType } = useStore();
  const [selectedColor, setSelectedColor] = useState('black');
  const [quantities, setQuantities] = useState({});
  const { cameraData } = useProductStore();
  const { getTheme } = useThemeStore();
  const theme = getTheme();

  // Get compatible cameras for all selected NVRs
  const compatibleCameraSkus = selectedNVRs.reduce((skus, nvr) => {
    const compatible = getCompatibleCameras(nvr.sku);
    return [...new Set([...skus, ...compatible])];
  }, []);

  // Filter cameras by recorder type and compatibility
  const filteredCameras = cameraData.filter(camera => {
    // First filter by recorder type
    if (camera.type !== recorderType) return false;
    
    // Then check compatibility if we have specific compatible cameras
    if (compatibleCameraSkus.length > 0) {
      return compatibleCameraSkus.includes(camera.sku);
    }
    
    // If no specific compatibility info, show all cameras of the correct type
    return true;
  });

  const handleCameraSelect = (camera) => {
    const quantity = quantities[camera.id] || 1;
    addCamera(camera, quantity, selectedColor);
    // Clear the quantity for this camera after adding it
    const newQuantities = { ...quantities };
    delete newQuantities[camera.id];
    setQuantities(newQuantities);
  };

  const handleRemoveCamera = (cameraId) => {
    removeCamera(cameraId);
  };

  const handleQuantityChange = (cameraId, newQuantity, isSelected) => {
    if (isSelected) {
      updateCameraQuantity(cameraId, newQuantity);
    } else {
      setQuantities(prev => ({
        ...prev,
        [cameraId]: newQuantity
      }));
    }
  };

  const getTotalCameras = () => {
    return selectedCameras.reduce((total, camera) => total + (camera.quantity || 1), 0);
  };

  const getQuantity = (camera, isSelected) => {
    if (isSelected) {
      const selectedCamera = selectedCameras.find(cam => cam.id === camera.id);
      return selectedCamera.quantity;
    }
    return quantities[camera.id] || 1;
  };

  const cameraTypeLabel = recorderType === 'digital' ? 'IP Cameras' : 'Analog Cameras';

  return (
    <div className="p-6">
      <h2 className={`text-xl font-semibold text-${theme.text} dark:text-gray-100 mb-6`}>
        Select {cameraTypeLabel}
      </h2>
      
      <ColorSelector 
        selectedColor={selectedColor} 
        onColorSelect={setSelectedColor} 
      />

      {filteredCameras.length === 0 ? (
        <div className="text-center py-8">
          <p className={`text-lg text-${theme.text} dark:text-gray-100`}>
            No compatible cameras found for the selected {recorderType === 'digital' ? 'NVR' : 'XVR'}.
          </p>
        </div>
      ) : (
        <div className="grid grid-cols-1 gap-6 sm:grid-cols-2 lg:grid-cols-3 mt-6">
          {filteredCameras.map((camera) => {
            const isSelected = selectedCameras.some(cam => cam.id === camera.id);
            const currentQuantity = getQuantity(camera, isSelected);
            
            return (
              <CameraCard
                key={camera.id}
                camera={camera}
                isSelected={isSelected}
                selectedColor={selectedColor}
                quantity={currentQuantity}
                maxQuantity={totalChannels - (getTotalCameras() - (isSelected ? currentQuantity : 0))}
                onSelect={handleCameraSelect}
                onRemove={handleRemoveCamera}
                onQuantityChange={(quantity) => handleQuantityChange(camera.id, quantity, isSelected)}
              />
            );
          })}
        </div>
      )}

      <div className="mt-8 flex justify-between">
        <button
          onClick={onBack}
          className={`px-4 py-2 border rounded-md hover:bg-${theme.background} dark:hover:bg-gray-700 text-${theme.text} dark:text-gray-100`}
        >
          Back
        </button>
        <button
          onClick={onNext}
          disabled={selectedCameras.length === 0}
          className={`px-4 py-2 bg-${theme.primary}-600 text-white rounded-md hover:bg-${theme.primary}-700 disabled:bg-gray-300 disabled:cursor-not-allowed`}
        >
          Continue
        </button>
      </div>
    </div>
  );
}