import { useDiscountStore } from '../store/discountStore';

export function calculateDiscounts(nvr, cameras, accessories) {
  const { discounts } = useDiscountStore.getState();
  const calculatedDiscounts = [];
  let totalDiscount = 0;

  // Camera bulk discount
  if (cameras.length >= discounts.bulkCamera.threshold) {
    const cameraSubtotal = cameras.reduce((total, camera) => total + (camera.price || 0), 0);
    const discount = (cameraSubtotal * discounts.bulkCamera.percentage) / 100;
    totalDiscount += discount;
    calculatedDiscounts.push({
      type: 'CAMERA_BULK',
      amount: discount,
      description: `Bulk Camera Discount (${discounts.bulkCamera.percentage}% off cameras for ${discounts.bulkCamera.threshold}+ cameras)`
    });
  }

  // Accessory bundle discount
  if (accessories.length >= discounts.accessoryBundle.threshold) {
    const accessorySubtotal = accessories.reduce((total, acc) => 
      total + ((acc.price || 0) * (acc.quantity || 1)), 0);
    const discount = (accessorySubtotal * discounts.accessoryBundle.percentage) / 100;
    totalDiscount += discount;
    calculatedDiscounts.push({
      type: 'ACCESSORY_BUNDLE',
      amount: discount,
      description: `Accessory Bundle Discount (${discounts.accessoryBundle.percentage}% off accessories when buying ${discounts.accessoryBundle.threshold}+ items)`
    });
  }

  // Complete system discount
  if (nvr && cameras.length > 0 && accessories.length > 0) {
    const subtotal = (nvr.price || 0) + 
      cameras.reduce((total, camera) => total + (camera.price || 0), 0) +
      accessories.reduce((total, acc) => total + ((acc.price || 0) * (acc.quantity || 1)), 0);
    const discount = (subtotal * discounts.completeSystem.percentage) / 100;
    totalDiscount += discount;
    calculatedDiscounts.push({
      type: 'COMPLETE_SYSTEM',
      amount: discount,
      description: `Complete System Discount (${discounts.completeSystem.percentage}% off total)`
    });
  }

  return { discounts: calculatedDiscounts, totalDiscount };
}