import { useState, useEffect } from 'react';
import { RecorderTypeSelector } from './RecorderTypeSelector';
import { NVRSelector } from './nvr/NVRSelector';
import { CameraSelector } from './camera/CameraSelector';
import { AccessorySelector } from './accessory/AccessorySelector';
import { BundleSummary } from './summary/BundleSummary';
import { CartSidebar } from './cart/CartSidebar';
import { useStore } from '../store';
import { useThemeStore } from '../store/themeStore';

export function BundleBuilder() {
  // Start at step 2 (NVR selection) instead of step 1
  const [step, setStep] = useState(2);
  const { selectedNVRs, selectedCameras, accessories, recorderType, setRecorderType } = useStore();
  const { getTheme } = useThemeStore();
  const theme = getTheme();

  // Auto-set recorder type to digital when component mounts
  useEffect(() => {
    setRecorderType('digital');
    console.log("BundleBuilder mounted, recorder type set to digital");
  }, [setRecorderType]);

  const nextStep = () => setStep(step + 1);
  const prevStep = () => setStep(step - 1);

  const getTotalChannels = () => {
    return selectedNVRs.reduce((total, nvr) => total + (nvr.channels * nvr.quantity), 0);
  };

  return (
    <div className="flex flex-col md:flex-row">
      <div className="flex-1 md:pr-80">
        <div className={`bg-${theme.card} dark:bg-gray-800 shadow sm:rounded-lg transition-colors duration-200`}>
          {/* Remove the recorder type selection step */}
          {/* {step === 1 && <RecorderTypeSelector onNext={nextStep} />} */}
          
          {step === 2 && <NVRSelector onNext={nextStep} onBack={null} />}
          {step === 3 && (
            <CameraSelector 
              onNext={nextStep} 
              onBack={prevStep}
              totalChannels={getTotalChannels()}
            />
          )}
          {step === 4 && <AccessorySelector onNext={nextStep} onBack={prevStep} />}
          {step === 5 && <BundleSummary onBack={prevStep} />}
        </div>
      </div>
      <CartSidebar />
    </div>
  );
}