import { HashRouter as Router, Routes, Route, Navigate } from 'react-router-dom';
import { BundleBuilder } from './components/BundleBuilder';
import { ThemeSelector } from './components/ThemeSelector';
import { useThemeStore } from './store/themeStore';
import { useEffect } from 'react';
import { Login } from './components/admin/Login';
import { Dashboard } from './components/admin/Dashboard';
import { useAuthStore } from './store/authStore';

function PrivateRoute({ children }) {
  const isAuthenticated = useAuthStore((state) => state.isAuthenticated);
  return isAuthenticated ? children : <Navigate to="/login" />;
}

export default function App() {
  const { isDark, getTheme } = useThemeStore();
  const theme = getTheme();

  useEffect(() => {
    if (isDark) {
      document.documentElement.classList.add('dark');
    } else {
      document.documentElement.classList.remove('dark');
    }
  }, [isDark]);

  return (
    <Router>
      <div className={`min-h-screen bg-${theme.background} dark:bg-gray-900 transition-colors duration-200`}>
        <ThemeSelector />
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-12">
          <Routes>
            <Route path="/" element={
              <>
                <h1 className={`text-3xl font-bold text-${theme.text} dark:text-gray-100 mb-8`}>
                  NVR System Bundle Builder
                </h1>
                <BundleBuilder />
              </>
            } />
            <Route path="/login" element={<Login />} />
            <Route
              path="/admin"
              element={
                <PrivateRoute>
                  <Dashboard />
                </PrivateRoute>
              }
            />
            <Route
              path="/admin/dashboard"
              element={
                <PrivateRoute>
                  <Dashboard />
                </PrivateRoute>
              }
            />
          </Routes>
        </div>
      </div>
    </Router>
  );
}