import create from 'zustand';

export const useStore = create((set) => ({
  recorderType: null, // 'digital' or 'analog'
  selectedNVRs: [],
  selectedCameras: [],
  accessories: [],
  
  setRecorderType: (type) => set({ recorderType: type }),
  
  addNVR: (nvr, quantity = 1, hddSize = 0) => set((state) => ({
    selectedNVRs: [...state.selectedNVRs, { ...nvr, quantity, hddSize }]
  })),
  
  updateNVRQuantity: (nvrId, quantity) => set((state) => ({
    selectedNVRs: state.selectedNVRs.map(nvr =>
      nvr.id === nvrId ? { ...nvr, quantity } : nvr
    )
  })),

  updateNVRHdd: (nvrId, hddSize) => set((state) => ({
    selectedNVRs: state.selectedNVRs.map(nvr =>
      nvr.id === nvrId ? { ...nvr, hddSize } : nvr
    )
  })),
  
  removeNVR: (nvrId) => set((state) => ({
    selectedNVRs: state.selectedNVRs.filter(nvr => nvr.id !== nvrId)
  })),
  
  addCamera: (camera, quantity = 1, color = 'black') => set((state) => ({
    selectedCameras: [...state.selectedCameras, { ...camera, quantity, color }]
  })),
  
  updateCameraQuantity: (cameraId, quantity) => set((state) => ({
    selectedCameras: state.selectedCameras.map(cam =>
      cam.id === cameraId ? { ...cam, quantity } : cam
    )
  })),
  
  removeCamera: (cameraId) => set((state) => ({
    selectedCameras: state.selectedCameras.filter(cam => cam.id !== cameraId)
  })),
  
  addAccessory: (accessory, quantity = 1) => set((state) => ({ 
    accessories: [...state.accessories, { ...accessory, quantity }] 
  })),
  
  updateAccessoryQuantity: (accessoryId, quantity) => set((state) => ({
    accessories: state.accessories.map(acc => 
      acc.id === accessoryId ? { ...acc, quantity } : acc
    )
  })),
  
  removeAccessory: (accessoryId) => set((state) => ({
    accessories: state.accessories.filter(a => a.id !== accessoryId)
  })),
  
  resetStore: () => set({ 
    recorderType: null,
    selectedNVRs: [], 
    selectedCameras: [], 
    accessories: [] 
  })
}));