import { ImageGallery } from '../common/ImageGallery';
import { useThemeStore } from '../../store/themeStore';
import { useProductStore } from '../../store/productStore';

export function NVRCard({ nvr, isSelected, onSelect, quantity = 1, onQuantityChange, onHddChange }) {
  const { getTheme } = useThemeStore();
  const theme = getTheme();
  const { hddOptions } = useProductStore();

  const handleClick = (e) => {
    e.stopPropagation();
    onSelect(nvr);
  };

  const handleQuantityChange = (e) => {
    e.stopPropagation();
    onQuantityChange(parseInt(e.target.value));
  };

  const handleHddChange = (e) => {
    e.stopPropagation();
    onHddChange(parseInt(e.target.value));
  };

  const getHddPrice = (size) => {
    const option = hddOptions.find(hdd => hdd.size === size);
    return option ? option.price : 0;
  };

  const totalPrice = (nvr.price + getHddPrice(nvr.hddSize || 0)) * quantity;

  return (
    <div
      className={`relative block border rounded-lg p-6 
        ${isSelected ? `border-${theme.primary}-500` : 'border-gray-200 dark:border-gray-700'}
        hover:border-${theme.primary}-500 focus:outline-none focus:ring-2 
        focus:ring-${theme.primary}-500 bg-${theme.card} dark:bg-gray-800`}
    >
      <div className="flex flex-col space-y-4">
        <ImageGallery images={nvr.images} />
        <div>
          <h3 className={`text-lg font-medium text-${theme.text} dark:text-gray-100`}>
            {nvr.name}
          </h3>
          <p className="text-sm text-gray-500 dark:text-gray-400">SKU: {nvr.sku}</p>
          <p className="text-sm text-gray-500 dark:text-gray-400">{nvr.channels} Channels</p>
          <p className={`text-sm font-medium text-${theme.primary}-600 dark:text-${theme.primary}-400`}>
            Base Price: ${nvr.price}
          </p>
          
          {isSelected && (
            <div className="space-y-4 mt-4">
              <div>
                <label className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-1">
                  Pre-installed HDD
                </label>
                <select
                  value={nvr.hddSize || 0}
                  onChange={handleHddChange}
                  className="mt-1 block w-full rounded-md border-gray-300 shadow-sm focus:border-indigo-500 focus:ring-indigo-500 dark:bg-gray-700 dark:border-gray-600"
                >
                  <option value={0}>No HDD</option>
                  {hddOptions.map(option => (
                    <option key={option.size} value={option.size}>
                      {option.label} (+${option.price})
                    </option>
                  ))}
                </select>
              </div>

              <div>
                <label className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-1">
                  Quantity
                </label>
                <input
                  type="range"
                  min="1"
                  max="5"
                  value={quantity}
                  onChange={handleQuantityChange}
                  className="w-full h-2 bg-gray-200 rounded-lg appearance-none cursor-pointer dark:bg-gray-700"
                />
                <div className="text-center mt-1">
                  <span className={`text-sm font-medium text-${theme.text} dark:text-gray-100`}>
                    {quantity}
                  </span>
                </div>
              </div>

              <p className={`text-lg font-bold text-${theme.primary}-600 dark:text-${theme.primary}-400`}>
                Total Price: ${totalPrice.toFixed(2)}
              </p>
            </div>
          )}

          <div className="mt-4">
            <button
              onClick={handleClick}
              className={`w-full px-4 py-2 ${
                isSelected 
                  ? 'bg-red-600 hover:bg-red-700 text-white' 
                  : `bg-${theme.primary}-600 hover:bg-${theme.primary}-700 text-white`
              } rounded-md transition-colors duration-200`}
            >
              {isSelected ? 'Remove from Bundle' : 'Add to Bundle'}
            </button>
          </div>
        </div>
      </div>
    </div>
  );
}