export function generateOrder(nvrs, cameras, accessories, recorderType) {
  const timestamp = new Date().toISOString();
  const orderId = `ORD-${Math.random().toString(36).substr(2, 9)}`;
  const systemType = recorderType === 'digital' ? 'Digital NVR' : 'Analog XVR';

  const orderLines = [
    `Order ID: ${orderId}`,
    `Date: ${timestamp}`,
    `System Type: ${systemType}`,
    '\nRecorder Systems:',
  ];

  // Add NVRs/XVRs
  nvrs.forEach(nvr => {
    orderLines.push(`${nvr.sku} x ${nvr.quantity} - ${nvr.name} (${nvr.channels} channels)`);
  });

  // Add Cameras
  orderLines.push('\nCameras:');
  cameras.forEach(camera => {
    orderLines.push(`${camera.sku} x ${camera.quantity} - ${camera.name} (${camera.color})`);
  });

  // Add Accessories if any
  if (accessories.length > 0) {
    orderLines.push('\nAccessories:');
    accessories.forEach(acc => {
      orderLines.push(`${acc.sku} x ${acc.quantity} - ${acc.name}`);
    });
  }

  return orderLines.join('\n');
}

export function exportOrder(orderContent) {
  const blob = new Blob([orderContent], { type: 'text/plain' });
  const url = URL.createObjectURL(blob);
  const link = document.createElement('a');
  link.href = url;
  link.download = 'order.txt';
  document.body.appendChild(link);
  link.click();
  document.body.removeChild(link);
  URL.revokeObjectURL(url);
}