import { ImageGallery } from '../common/ImageGallery';
import { useThemeStore } from '../../store/themeStore';

export function AccessoryCard({ accessory, isSelected, onSelect, quantity = 1, onQuantityChange }) {
  const { getTheme } = useThemeStore();
  const theme = getTheme();

  const handleClick = (e) => {
    e.stopPropagation();
    onSelect(accessory);
  };

  const handleQuantityChange = (e) => {
    e.stopPropagation();
    onQuantityChange(parseInt(e.target.value));
  };

  return (
    <div
      className={`relative block border rounded-lg p-6 
        ${isSelected ? `border-${theme.primary}-500` : 'border-gray-200 dark:border-gray-700'}
        hover:border-${theme.primary}-500 focus:outline-none focus:ring-2 
        focus:ring-${theme.primary}-500 bg-${theme.card} dark:bg-gray-800`}
    >
      <div className="flex flex-col space-y-4">
        <ImageGallery images={accessory.images} />
        <div>
          <h3 className={`text-lg font-medium text-${theme.text} dark:text-gray-100`}>
            {accessory.name}
          </h3>
          <p className="text-sm text-gray-500 dark:text-gray-400">SKU: {accessory.sku}</p>
          {accessory.length && (
            <p className="text-sm text-gray-500 dark:text-gray-400">Length: {accessory.length}</p>
          )}
          {accessory.capacity && (
            <p className="text-sm text-gray-500 dark:text-gray-400">Capacity: {accessory.capacity}</p>
          )}
          {accessory.ports && (
            <p className="text-sm text-gray-500 dark:text-gray-400">Ports: {accessory.ports}</p>
          )}
          <p className={`text-sm font-medium text-${theme.primary}-600 dark:text-${theme.primary}-400`}>
            ${accessory.price}
          </p>
          {isSelected && (
            <div className="mt-4">
              <label className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-1">
                Quantity
              </label>
              <input
                type="range"
                min="1"
                max="10"
                value={quantity}
                onChange={handleQuantityChange}
                className="w-full h-2 bg-gray-200 rounded-lg appearance-none cursor-pointer dark:bg-gray-700"
              />
              <div className="text-center mt-1">
                <span className={`text-sm font-medium text-${theme.text} dark:text-gray-100`}>
                  {quantity}
                </span>
              </div>
            </div>
          )}
          <div className="mt-4">
            <button
              onClick={handleClick}
              className={`w-full px-4 py-2 ${
                isSelected 
                  ? 'bg-red-600 hover:bg-red-700 text-white' 
                  : `bg-${theme.primary}-600 hover:bg-${theme.primary}-700 text-white`
              } rounded-md transition-colors duration-200`}
            >
              {isSelected ? 'Remove from Bundle' : 'Add to Bundle'}
            </button>
          </div>
        </div>
      </div>
    </div>
  );
}