import { Menu, Transition } from '@headlessui/react';
import { SunIcon, MoonIcon, SwatchIcon } from '@heroicons/react/24/outline';
import { Fragment } from 'react';
import { useThemeStore } from '../store/themeStore';

export function ThemeSelector() {
  const { currentTheme, isDark, setTheme, toggleDarkMode } = useThemeStore();

  const themes = [
    { name: 'Forest', value: 'forest', color: 'bg-emerald-500' },
    { name: 'Ocean', value: 'ocean', color: 'bg-blue-500' },
    { name: 'Sunset', value: 'sunset', color: 'bg-rose-500' },
  ];

  return (
    <div className="fixed top-4 right-4 flex items-center space-x-2 z-50">
      <button
        onClick={toggleDarkMode}
        className="p-2 rounded-lg bg-white dark:bg-gray-800 hover:bg-gray-100 dark:hover:bg-gray-700 shadow-md"
        aria-label={isDark ? 'Switch to light mode' : 'Switch to dark mode'}
      >
        {isDark ? (
          <SunIcon className="h-5 w-5 text-gray-600 dark:text-gray-300" />
        ) : (
          <MoonIcon className="h-5 w-5 text-gray-600 dark:text-gray-300" />
        )}
      </button>

      <Menu as="div" className="relative">
        <Menu.Button className="p-2 rounded-lg bg-white dark:bg-gray-800 hover:bg-gray-100 dark:hover:bg-gray-700 shadow-md">
          <SwatchIcon className="h-5 w-5 text-gray-600 dark:text-gray-300" />
        </Menu.Button>
        <Transition
          as={Fragment}
          enter="transition ease-out duration-100"
          enterFrom="transform opacity-0 scale-95"
          enterTo="transform opacity-100 scale-100"
          leave="transition ease-in duration-75"
          leaveFrom="transform opacity-100 scale-100"
          leaveTo="transform opacity-0 scale-95"
        >
          <Menu.Items className="absolute right-0 mt-2 w-48 origin-top-right rounded-md bg-white dark:bg-gray-800 shadow-lg ring-1 ring-black ring-opacity-5 focus:outline-none">
            <div className="py-1">
              {themes.map((theme) => (
                <Menu.Item key={theme.value}>
                  {({ active }) => (
                    <button
                      onClick={() => setTheme(theme.value)}
                      className={`
                        ${active ? 'bg-gray-100 dark:bg-gray-700' : ''}
                        ${currentTheme === theme.value ? 'bg-gray-50 dark:bg-gray-700' : ''}
                        group flex items-center w-full px-4 py-2 text-sm text-gray-700 dark:text-gray-300
                      `}
                    >
                      <div className={`w-4 h-4 rounded-full mr-3 ${theme.color}`} />
                      {theme.name}
                    </button>
                  )}
                </Menu.Item>
              ))}
            </div>
          </Menu.Items>
        </Transition>
      </Menu>
    </div>
  );
}