// src/utils/productImporter.js
import Papa from 'papaparse';

/**
 * Import products from a WooCommerce CSV export file
 * 
 * This utility allows importing products from a WooCommerce CSV export
 * and mapping them to the bundle builder's data structure
 */

/**
 * Parse the WooCommerce CSV export file
 * 
 * @param {File} file - The CSV file to parse
 * @returns {Promise<Array>} - The parsed products
 */
export async function parseWooCommerceCSV(file) {
  return new Promise((resolve, reject) => {
    Papa.parse(file, {
      header: true,
      dynamicTyping: true,
      skipEmptyLines: true,
      complete: (results) => {
        resolve(results.data);
      },
      error: (error) => {
        reject(error);
      }
    });
  });
}

/**
 * Convert WooCommerce product data to bundle builder format
 * 
 * @param {Array} wcProducts - Array of WooCommerce products
 * @returns {Object} - Object containing mappable products by category
 */
export function mapWooCommerceProducts(wcProducts) {
  const products = {
    nvr: [],
    camera: [],
    accessory: [],
    other: [],
    // Map of SKU to WooCommerce product ID
    skuToProductId: {}
  };

  // First, build the SKU to ID mapping
  wcProducts.forEach(product => {
    if (product.SKU && product.ID) {
      products.skuToProductId[product.SKU] = product.ID;
    }
  });

  // Then categorize and convert products
  wcProducts.forEach(product => {
    if (!product.Type || product.Type === 'variation' || !product.SKU) {
      return; // Skip variations and products without SKUs
    }

    const baseProduct = {
      id: product.ID,
      name: product.Name,
      sku: product.SKU,
      price: parseFloat(product['Regular price']) || 0,
      wcProductId: product.ID,
      images: parseWooCommerceImages(product.Images),
      type: detectProductType(product)
    };

    // Categorize product based on SKU patterns or categories
    if (isNVRProduct(product)) {
      products.nvr.push({
        ...baseProduct,
        channels: detectChannels(product),
      });
    } 
    else if (isCameraProduct(product)) {
      // Handle camera attributes
      products.camera.push({
        ...baseProduct,
        resolution: detectResolution(product),
        images: mapCameraImages(product)
      });
    } 
    else if (isAccessoryProduct(product)) {
      products.accessory.push(baseProduct);
    } 
    else {
      products.other.push(baseProduct);
    }
  });

  return products;
}

/**
 * Parse WooCommerce image URLs from the Images column
 * 
 * @param {string} imagesStr - Comma-separated image URLs
 * @returns {Array} - Array of image URLs
 */
function parseWooCommerceImages(imagesStr) {
  if (!imagesStr) return [];
  
  return imagesStr.split(',')
    .map(url => url.trim())
    .filter(url => url.length > 0);
}

/**
 * Detect if a product is an NVR/XVR
 * 
 * @param {Object} product - WooCommerce product
 * @returns {boolean} - True if the product is an NVR
 */
function isNVRProduct(product) {
  // Check SKU pattern
  if (product.SKU && (product.SKU.includes('NVR') || product.SKU.includes('XVR'))) {
    return true;
  }
  
  // Check categories
  if (product.Categories && (
    product.Categories.includes('NVR') || 
    product.Categories.includes('DVR') || 
    product.Categories.includes('Recorder')
  )) {
    return true;
  }
  
  // Check product name
  if (product.Name && (
    product.Name.includes('NVR') || 
    product.Name.includes('XVR') || 
    product.Name.includes('DVR') ||
    product.Name.includes('Network Video Recorder') ||
    product.Name.includes('Digital Video Recorder')
  )) {
    return true;
  }
  
  return false;
}

/**
 * Detect if a product is a camera
 * 
 * @param {Object} product - WooCommerce product
 * @returns {boolean} - True if the product is a camera
 */
function isCameraProduct(product) {
  // Check SKU pattern
  if (product.SKU && (product.SKU.includes('CAM'))) {
    return true;
  }
  
  // Check categories
  if (product.Categories && (
    product.Categories.includes('Camera') || 
    product.Categories.includes('Cameras')
  )) {
    return true;
  }
  
  // Check product name
  if (product.Name && (
    product.Name.includes('Camera') ||
    product.Name.includes('Dome') ||
    product.Name.includes('Bullet') ||
    product.Name.includes('PTZ')
  )) {
    return true;
  }
  
  return false;
}

/**
 * Detect if a product is an accessory
 * 
 * @param {Object} product - WooCommerce product
 * @returns {boolean} - True if the product is an accessory
 */
function isAccessoryProduct(product) {
  // Check SKU pattern
  if (product.SKU && (product.SKU.includes('ACC'))) {
    return true;
  }
  
  // Check categories
  if (product.Categories && (
    product.Categories.includes('Accessory') || 
    product.Categories.includes('Accessories') ||
    product.Categories.includes('Cable') ||
    product.Categories.includes('Storage')
  )) {
    return true;
  }
  
  // Check product name for common accessories
  if (product.Name && (
    product.Name.includes('Cable') ||
    product.Name.includes('HDD') ||
    product.Name.includes('Hard Drive') ||
    product.Name.includes('Switch') ||
    product.Name.includes('Power Supply') ||
    product.Name.includes('Connector')
  )) {
    return true;
  }
  
  return false;
}

/**
 * Detect the product type (digital, analog, or both)
 * 
 * @param {Object} product - WooCommerce product
 * @returns {string} - The product type: 'digital', 'analog', or 'both'
 */
function detectProductType(product) {
  // Check SKU
  if (product.SKU) {
    if (product.SKU.includes('NVR') || product.SKU.includes('IP')) {
      return 'digital';
    }
    if (product.SKU.includes('XVR') || product.SKU.includes('DVR') || product.SKU.includes('ANALOG')) {
      return 'analog';
    }
  }
  
  // Check attributes
  for (let i = 1; i <= 10; i++) {
    const attrName = product[`Attribute ${i} name`];
    const attrValue = product[`Attribute ${i} value(s)`];
    
    if (attrName === 'Type' && attrValue) {
      if (attrValue.includes('Digital') || attrValue.includes('IP')) {
        return 'digital';
      }
      if (attrValue.includes('Analog') || attrValue.includes('HDCVI')) {
        return 'analog';
      }
      if (attrValue.includes('Both') || attrValue.includes('Compatible')) {
        return 'both';
      }
    }
  }
  
  // Check product name
  if (product.Name) {
    if (product.Name.includes('IP Camera') || product.Name.includes('NVR')) {
      return 'digital';
    }
    if (product.Name.includes('Analog') || product.Name.includes('HDCVI') || product.Name.includes('DVR')) {
      return 'analog';
    }
  }
  
  // Check categories
  if (product.Categories) {
    if (product.Categories.includes('IP Camera') || product.Categories.includes('NVR')) {
      return 'digital';
    }
    if (product.Categories.includes('Analog') || product.Categories.includes('HDCVI') || product.Categories.includes('DVR')) {
      return 'analog';
    }
  }
  
  // Default to digital if we can't determine
  return 'digital';
}

/**
 * Detect the number of channels for an NVR
 * 
 * @param {Object} product - WooCommerce product
 * @returns {number} - The number of channels
 */
function detectChannels(product) {
  // Check attributes
  for (let i = 1; i <= 10; i++) {
    const attrName = product[`Attribute ${i} name`];
    const attrValue = product[`Attribute ${i} value(s)`];
    
    if ((attrName === 'Channels' || attrName === 'Channel Count') && attrValue) {
      const channelNumber = parseInt(attrValue);
      if (!isNaN(channelNumber)) {
        return channelNumber;
      }
    }
  }
  
  // Check product name for channel numbers
  if (product.Name) {
    const channelMatch = product.Name.match(/(\d+)[\s-]?ch/i) || 
                         product.Name.match(/(\d+)[\s-]?channel/i);
    if (channelMatch && channelMatch[1]) {
      const channelNumber = parseInt(channelMatch[1]);
      if (!isNaN(channelNumber)) {
        return channelNumber;
      }
    }
  }
  
  // Check SKU for channel numbers
  if (product.SKU) {
    // Look for numbers that might represent channels
    const matches = product.SKU.match(/[-_](\d+)(?:CH)?$/) || // Matches patterns like NVR-16CH or NVR-16
                   product.SKU.match(/(\d+)CH/i);            // Matches patterns like NVR16CH
    
    if (matches && matches[1]) {
      const channelNumber = parseInt(matches[1]);
      if (!isNaN(channelNumber)) {
        return channelNumber;
      }
    }
  }
  
  // Default to 4 channels if we can't determine
  return 4;
}

/**
 * Detect the resolution of a camera
 * 
 * @param {Object} product - WooCommerce product
 * @returns {string} - The camera resolution
 */
function detectResolution(product) {
  // Check attributes
  for (let i = 1; i <= 10; i++) {
    const attrName = product[`Attribute ${i} name`];
    const attrValue = product[`Attribute ${i} value(s)`];
    
    if (attrName === 'Resolution' && attrValue) {
      return attrValue;
    }
  }
  
  // Check product name for common resolutions
  if (product.Name) {
    if (product.Name.includes('4K')) return '4K (8MP)';
    if (product.Name.includes('8MP')) return '8MP (4K)';
    if (product.Name.includes('5MP')) return '5MP (2560x1920)';
    if (product.Name.includes('4MP')) return '4MP (2560x1440)';
    if (product.Name.includes('3MP')) return '3MP (2048x1536)';
    if (product.Name.includes('2MP') || product.Name.includes('1080P')) return '2MP (1920x1080)';
    if (product.Name.includes('1MP') || product.Name.includes('720P')) return '1MP (1280x720)';
  }
  
  // Default to HD if we can't determine
  return '2MP (1920x1080)';
}

/**
 * Map camera images, handling color variants
 * 
 * @param {Object} product - WooCommerce product
 * @returns {Object} - Object with black and white image arrays
 */
function mapCameraImages(product) {
  const images = parseWooCommerceImages(product.Images);
  const result = { black: [], white: [] };
  
  // Attempt to classify images by filename
  images.forEach(url => {
    const lowercaseUrl = url.toLowerCase();
    
    if (lowercaseUrl.includes('white')) {
      result.white.push(url);
    } else if (lowercaseUrl.includes('black') || 
               !lowercaseUrl.includes('white')) { // Default to black if not specified
      result.black.push(url);
    }
  });
  
  // If no images were classified by color, put all in black
  if (result.black.length === 0 && result.white.length === 0) {
    result.black = images;
  }
  
  return result;
}

/**
 * Generate compatibility mapping between NVRs and cameras
 * 
 * @param {Object} products - Object containing product categories
 * @returns {Object} - Compatibility mapping
 */
export function generateCompatibilityMap(products) {
  const compatibilityMap = {};
  
  // For each NVR, determine compatible cameras
  products.nvr.forEach(nvr => {
    const compatibleCameras = [];
    
    // Basic compatibility rule: match by type (digital/analog)
    products.camera.forEach(camera => {
      if (nvr.type === camera.type) {
        compatibleCameras.push(camera.sku);
      }
    });
    
    compatibilityMap[nvr.sku] = compatibleCameras;
  });
  
  return compatibilityMap;
}

/**
 * Save selected products to local storage
 * 
 * @param {Object} selectedProducts - Object containing selected products by category
 */
export function saveSelectedProducts(selectedProducts) {
  localStorage.setItem('bundle-builder-products', JSON.stringify(selectedProducts));
}

/**
 * Load selected products from local storage
 * 
 * @returns {Object|null} - Object containing selected products or null if not found
 */
export function loadSelectedProducts() {
  const savedProducts = localStorage.getItem('bundle-builder-products');
  
  if (savedProducts) {
    try {
      return JSON.parse(savedProducts);
    } catch (error) {
      console.error('Error parsing saved products:', error);
      return null;
    }
  }
  
  return null;
}

/**
 * Export selected products to a file
 * 
 * @param {Object} selectedProducts - Object containing selected products
 */
export function exportSelectedProducts(selectedProducts) {
  const dataStr = JSON.stringify(selectedProducts, null, 2);
  const dataUri = 'data:application/json;charset=utf-8,'+ encodeURIComponent(dataStr);
  
  const exportFileDefaultName = 'bundle-builder-products.json';
  
  const linkElement = document.createElement('a');
  linkElement.setAttribute('href', dataUri);
  linkElement.setAttribute('download', exportFileDefaultName);
  linkElement.click();
}

/**
 * Import products from a JSON file
 * 
 * @param {File} file - The JSON file to import
 * @returns {Promise<Object>} - The imported products
 */
export async function importProductsFromJson(file) {
  return new Promise((resolve, reject) => {
    const reader = new FileReader();
    
    reader.onload = (event) => {
      try {
        const products = JSON.parse(event.target.result);
        resolve(products);
      } catch (error) {
        reject(error);
      }
    };
    
    reader.onerror = (error) => {
      reject(error);
    };
    
    reader.readAsText(file);
  });
}