// src/components/admin/ProductImport.jsx
import { useState, useRef } from 'react';
import { useProductStore } from '../../store/productStore';
import { parseWooCommerceCSV, mapWooCommerceProducts, generateCompatibilityMap, saveSelectedProducts, exportSelectedProducts } from '../../utils/productImporter';
import { useThemeStore } from '../../store/themeStore';

export function ProductImport() {
  const [importStatus, setImportStatus] = useState('idle'); // idle, importing, success, error
  const [importedProducts, setImportedProducts] = useState(null);
  const [selectedProducts, setSelectedProducts] = useState({
    nvr: [],
    camera: [],
    accessory: []
  });
  const [error, setError] = useState('');
  const [stats, setStats] = useState(null);
  const fileInputRef = useRef(null);
  
  const { nvrData, cameraData, accessoryData, setProducts, setCompatibilityMap, setSkuToProductIdMap } = useProductStore();
  const { getTheme } = useThemeStore();
  const theme = getTheme();
  
  // Handle file upload and parsing
  const handleFileUpload = async (event) => {
    const file = event.target.files[0];
    if (!file) return;
    
    setImportStatus('importing');
    setError('');
    
    try {
      // Parse the CSV file
      const parsedData = await parseWooCommerceCSV(file);
      
      // Map to bundle builder format
      const mappedProducts = mapWooCommerceProducts(parsedData);
      
      // Set the imported products
      setImportedProducts(mappedProducts);
      
      // Generate compatibility mapping
      const compatMap = generateCompatibilityMap(mappedProducts);
      
      // Set stats
      setStats({
        total: parsedData.length,
        nvr: mappedProducts.nvr.length,
        camera: mappedProducts.camera.length,
        accessory: mappedProducts.accessory.length,
        other: mappedProducts.other.length
      });
      
      // Initialize selected products with all imported products
      setSelectedProducts({
        nvr: [...mappedProducts.nvr],
        camera: [...mappedProducts.camera],
        accessory: [...mappedProducts.accessory]
      });
      
      setImportStatus('success');
    } catch (error) {
      console.error('Error importing products:', error);
      setError(`Error importing products: ${error.message}`);
      setImportStatus('error');
    }
  };
  
  // Toggle product selection
  const toggleProduct = (category, productId) => {
    setSelectedProducts(prevSelected => {
      const categoryProducts = prevSelected[category];
      const isSelected = categoryProducts.some(p => p.id === productId);
      
      if (isSelected) {
        // Remove from selection
        return {
          ...prevSelected,
          [category]: categoryProducts.filter(p => p.id !== productId)
        };
      } else {
        // Add to selection
        const productToAdd = importedProducts[category].find(p => p.id === productId);
        return {
          ...prevSelected,
          [category]: [...categoryProducts, productToAdd]
        };
      }
    });
  };
  
  // Apply selected products to the bundle builder
  const applySelectedProducts = () => {
    // Set the products in the store
    setProducts('nvr', selectedProducts.nvr);
    setProducts('camera', selectedProducts.camera);
    setProducts('accessory', selectedProducts.accessory);
    
    // Generate and set compatibility map
    const compatMap = generateCompatibilityMap(selectedProducts);
    setCompatibilityMap(compatMap);
    
    // Set the SKU to product ID mapping
    if (importedProducts && importedProducts.skuToProductId) {
      setSkuToProductIdMap(importedProducts.skuToProductId);
    }
    
    // Save selected products to local storage
    saveSelectedProducts({
      ...selectedProducts,
      skuToProductId: importedProducts?.skuToProductId || {}
    });
    
    alert('Products have been applied to the bundle builder!');
  };
  
  // Export selected products
  const handleExport = () => {
    exportSelectedProducts({
      ...selectedProducts,
      skuToProductId: importedProducts?.skuToProductId || {}
    });
  };
  
  // Select all products in a category
  const selectAllInCategory = (category) => {
    setSelectedProducts(prev => ({
      ...prev,
      [category]: [...importedProducts[category]]
    }));
  };
  
  // Deselect all products in a category
  const deselectAllInCategory = (category) => {
    setSelectedProducts(prev => ({
      ...prev,
      [category]: []
    }));
  };
  
  return (
    <div className={`bg-${theme.card} dark:bg-gray-800 shadow sm:rounded-lg p-6 transition-colors duration-200`}>
      <h2 className={`text-xl font-semibold text-${theme.text} dark:text-gray-100 mb-6`}>
        WooCommerce Product Import
      </h2>
      
      <div className="mb-6">
        <p className={`text-${theme.text} dark:text-gray-200 mb-4`}>
          Import products from a WooCommerce CSV export to populate your bundle builder.
        </p>
        
        <div className="flex flex-col md:flex-row space-y-4 md:space-y-0 md:space-x-4">
          <div className="flex-1">
            <label className={`block text-sm font-medium text-${theme.text} dark:text-gray-300 mb-2`}>
              Upload WooCommerce CSV Export
            </label>
            <input
              type="file"
              ref={fileInputRef}
              accept=".csv"
              onChange={handleFileUpload}
              className={`block w-full text-sm text-${theme.text} dark:text-gray-300
                         file:mr-4 file:py-2 file:px-4
                         file:rounded-full file:border-0
                         file:text-sm file:font-semibold
                         file:bg-${theme.primary}-50 file:text-${theme.primary}-700
                         hover:file:bg-${theme.primary}-100
                         dark:file:bg-${theme.primary}-900 dark:file:text-${theme.primary}-200
                         dark:hover:file:bg-${theme.primary}-800`}
            />
          </div>
          
          {importStatus === 'success' && (
            <button
              onClick={applySelectedProducts}
              className={`px-4 py-2 bg-${theme.primary}-600 hover:bg-${theme.primary}-700 text-white rounded-md font-medium`}
            >
              Apply Selected Products
            </button>
          )}
          
          {importStatus === 'success' && (
            <button
              onClick={handleExport}
              className={`px-4 py-2 border border-${theme.primary}-600 text-${theme.primary}-600 hover:bg-${theme.primary}-50 rounded-md font-medium`}
            >
              Export Selected
            </button>
          )}
        </div>
      </div>
      
      {importStatus === 'importing' && (
        <div className={`p-4 bg-${theme.background} dark:bg-gray-700 rounded-lg`}>
          <p className={`text-${theme.text} dark:text-gray-200`}>Importing products...</p>
        </div>
      )}
      
      {importStatus === 'error' && (
        <div className="p-4 bg-red-100 dark:bg-red-900 rounded-lg">
          <p className="text-red-700 dark:text-red-200">{error}</p>
        </div>
      )}
      
      {importStatus === 'success' && stats && (
        <div className={`p-4 bg-${theme.background} dark:bg-gray-700 rounded-lg mb-6`}>
          <h3 className={`text-lg font-medium text-${theme.text} dark:text-gray-100 mb-2`}>Import Summary</h3>
          <div className="grid grid-cols-2 md:grid-cols-4 gap-4">
            <div className={`p-3 bg-${theme.card} dark:bg-gray-800 rounded-lg text-center`}>
              <p className={`text-${theme.text} dark:text-gray-300 text-sm`}>Total Products</p>
              <p className={`text-2xl font-bold text-${theme.primary}-600 dark:text-${theme.primary}-400`}>{stats.total}</p>
            </div>
            <div className={`p-3 bg-${theme.card} dark:bg-gray-800 rounded-lg text-center`}>
              <p className={`text-${theme.text} dark:text-gray-300 text-sm`}>NVR Systems</p>
              <p className={`text-2xl font-bold text-${theme.primary}-600 dark:text-${theme.primary}-400`}>{stats.nvr}</p>
            </div>
            <div className={`p-3 bg-${theme.card} dark:bg-gray-800 rounded-lg text-center`}>
              <p className={`text-${theme.text} dark:text-gray-300 text-sm`}>Cameras</p>
              <p className={`text-2xl font-bold text-${theme.primary}-600 dark:text-${theme.primary}-400`}>{stats.camera}</p>
            </div>
            <div className={`p-3 bg-${theme.card} dark:bg-gray-800 rounded-lg text-center`}>
              <p className={`text-${theme.text} dark:text-gray-300 text-sm`}>Accessories</p>
              <p className={`text-2xl font-bold text-${theme.primary}-600 dark:text-${theme.primary}-400`}>{stats.accessory}</p>
            </div>
          </div>
        </div>
      )}
      
      {importStatus === 'success' && importedProducts && (
        <div className="mt-8 space-y-8">
          {/* NVR Products */}
          <div>
            <div className="flex justify-between items-center mb-4">
              <h3 className={`text-lg font-medium text-${theme.text} dark:text-gray-100`}>
                NVR Systems ({selectedProducts.nvr.length}/{importedProducts.nvr.length})
              </h3>
              <div className="space-x-2">
                <button 
                  onClick={() => selectAllInCategory('nvr')}
                  className={`px-2 py-1 text-xs bg-${theme.primary}-100 text-${theme.primary}-700 rounded hover:bg-${theme.primary}-200`}
                >
                  Select All
                </button>
                <button 
                  onClick={() => deselectAllInCategory('nvr')}
                  className="px-2 py-1 text-xs bg-gray-100 text-gray-700 rounded hover:bg-gray-200"
                >
                  Deselect All
                </button>
              </div>
            </div>
            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
              {importedProducts.nvr.map(product => {
                const isSelected = selectedProducts.nvr.some(p => p.id === product.id);
                return (
                  <div 
                    key={product.id} 
                    className={`p-4 border rounded-lg cursor-pointer ${
                      isSelected 
                        ? `border-${theme.primary}-500 bg-${theme.primary}-50 dark:border-${theme.primary}-700 dark:bg-${theme.primary}-900`
                        : 'border-gray-200 hover:border-gray-300 dark:border-gray-700 dark:hover:border-gray-600'
                    }`}
                    onClick={() => toggleProduct('nvr', product.id)}
                  >
                    <div className="flex items-start">
                      <div className="flex-shrink-0 mr-4">
                        <input 
                          type="checkbox" 
                          checked={isSelected}
                          onChange={() => {}}
                          className={`h-5 w-5 text-${theme.primary}-600 focus:ring-${theme.primary}-500 border-gray-300 rounded`}
                        />
                      </div>
                      <div className="flex-1">
                        <h4 className={`text-md font-medium text-${theme.text} dark:text-gray-200`}>{product.name}</h4>
                        <p className="text-sm text-gray-500 dark:text-gray-400">SKU: {product.sku}</p>
                        <p className="text-sm text-gray-500 dark:text-gray-400">Price: ${product.price}</p>
                        <p className="text-sm text-gray-500 dark:text-gray-400">Channels: {product.channels}</p>
                        <p className="text-sm text-gray-500 dark:text-gray-400">
                          Type: {product.type === 'digital' ? 'Digital (NVR)' : 'Analog (XVR)'}
                        </p>
                      </div>
                    </div>
                  </div>
                );
              })}
            </div>
          </div>
          
          {/* Camera Products */}
          <div>
            <div className="flex justify-between items-center mb-4">
              <h3 className={`text-lg font-medium text-${theme.text} dark:text-gray-100`}>
                Cameras ({selectedProducts.camera.length}/{importedProducts.camera.length})
              </h3>
              <div className="space-x-2">
                <button 
                  onClick={() => selectAllInCategory('camera')}
                  className={`px-2 py-1 text-xs bg-${theme.primary}-100 text-${theme.primary}-700 rounded hover:bg-${theme.primary}-200`}
                >
                  Select All
                </button>
                <button 
                  onClick={() => deselectAllInCategory('camera')}
                  className="px-2 py-1 text-xs bg-gray-100 text-gray-700 rounded hover:bg-gray-200"
                >
                  Deselect All
                </button>
              </div>
            </div>
            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
              {importedProducts.camera.map(product => {
                const isSelected = selectedProducts.camera.some(p => p.id === product.id);
                return (
                  <div 
                    key={product.id} 
                    className={`p-4 border rounded-lg cursor-pointer ${
                      isSelected 
                        ? `border-${theme.primary}-500 bg-${theme.primary}-50 dark:border-${theme.primary}-700 dark:bg-${theme.primary}-900`
                        : 'border-gray-200 hover:border-gray-300 dark:border-gray-700 dark:hover:border-gray-600'
                    }`}
                    onClick={() => toggleProduct('camera', product.id)}
                  >
                    <div className="flex items-start">
                      <div className="flex-shrink-0 mr-4">
                        <input 
                          type="checkbox" 
                          checked={isSelected}
                          onChange={() => {}}
                          className={`h-5 w-5 text-${theme.primary}-600 focus:ring-${theme.primary}-500 border-gray-300 rounded`}
                        />
                      </div>
                      <div className="flex-1">
                        <h4 className={`text-md font-medium text-${theme.text} dark:text-gray-200`}>{product.name}</h4>
                        <p className="text-sm text-gray-500 dark:text-gray-400">SKU: {product.sku}</p>
                        <p className="text-sm text-gray-500 dark:text-gray-400">Price: ${product.price}</p>
                        <p className="text-sm text-gray-500 dark:text-gray-400">Resolution: {product.resolution}</p>
                        <p className="text-sm text-gray-500 dark:text-gray-400">
                          Type: {product.type === 'digital' ? 'Digital (IP)' : 'Analog'}
                        </p>
                      </div>
                    </div>
                  </div>
                );
              })}
            </div>
          </div>
          
          {/* Accessory Products */}
          <div>
            <div className="flex justify-between items-center mb-4">
              <h3 className={`text-lg font-medium text-${theme.text} dark:text-gray-100`}>
                Accessories ({selectedProducts.accessory.length}/{importedProducts.accessory.length})
              </h3>
              <div className="space-x-2">
                <button 
                  onClick={() => selectAllInCategory('accessory')}
                  className={`px-2 py-1 text-xs bg-${theme.primary}-100 text-${theme.primary}-700 rounded hover:bg-${theme.primary}-200`}
                >
                  Select All
                </button>
                <button 
                  onClick={() => deselectAllInCategory('accessory')}
                  className="px-2 py-1 text-xs bg-gray-100 text-gray-700 rounded hover:bg-gray-200"
                >
                  Deselect All
                </button>
              </div>
            </div>
            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
              {importedProducts.accessory.map(product => {
                const isSelected = selectedProducts.accessory.some(p => p.id === product.id);
                return (
                  <div 
                    key={product.id} 
                    className={`p-4 border rounded-lg cursor-pointer ${
                      isSelected 
                        ? `border-${theme.primary}-500 bg-${theme.primary}-50 dark:border-${theme.primary}-700 dark:bg-${theme.primary}-900`
                        : 'border-gray-200 hover:border-gray-300 dark:border-gray-700 dark:hover:border-gray-600'
                    }`}
                    onClick={() => toggleProduct('accessory', product.id)}
                  >
                    <div className="flex items-start">
                      <div className="flex-shrink-0 mr-4">
                        <input 
                          type="checkbox" 
                          checked={isSelected}
                          onChange={() => {}}
                          className={`h-5 w-5 text-${theme.primary}-600 focus:ring-${theme.primary}-500 border-gray-300 rounded`}
                        />
                      </div>
                      <div className="flex-1">
                        <h4 className={`text-md font-medium text-${theme.text} dark:text-gray-200`}>{product.name}</h4>
                        <p className="text-sm text-gray-500 dark:text-gray-400">SKU: {product.sku}</p>
                        <p className="text-sm text-gray-500 dark:text-gray-400">Price: ${product.price}</p>
                        <p className="text-sm text-gray-500 dark:text-gray-400">
                          Compatible with: {product.type === 'digital' ? 'Digital Systems' : product.type === 'analog' ? 'Analog Systems' : 'All Systems'}
                        </p>
                      </div>
                    </div>
                  </div>
                );
              })}
            </div>
          </div>
          
          {/* Other Products */}
          {importedProducts.other.length > 0 && (
            <div>
              <h3 className={`text-lg font-medium text-${theme.text} dark:text-gray-100 mb-4`}>
                Other Products ({importedProducts.other.length})
              </h3>
              <div className={`p-4 bg-${theme.background} dark:bg-gray-700 rounded-lg`}>
                <p className={`text-${theme.text} dark:text-gray-200 mb-2`}>
                  These products could not be automatically categorized as NVR systems, cameras, or accessories.
                </p>
                <details className="mt-2">
                  <summary className={`text-sm text-${theme.primary}-600 cursor-pointer`}>
                    View Uncategorized Products
                  </summary>
                  <ul className="mt-2 pl-4 space-y-1">
                    {importedProducts.other.map(product => (
                      <li key={product.id} className={`text-sm text-${theme.text} dark:text-gray-300`}>
                        {product.name} (SKU: {product.sku})
                      </li>
                    ))}
                  </ul>
                </details>
              </div>
            </div>
          )}
        </div>
      )}
    </div>
  );
}