import { useState, useRef } from 'react';
import { XMarkIcon, PhotoIcon, LinkIcon } from '@heroicons/react/24/outline';

export function ImageUploadField({ images = [], onChange, maxImages = 4 }) {
  const [currentImages, setCurrentImages] = useState(Array.isArray(images) ? images : []);
  const [uploadType, setUploadType] = useState('url');
  const fileInputRef = useRef(null);

  const handleImageAdd = () => {
    if (currentImages.length < maxImages) {
      if (uploadType === 'file') {
        fileInputRef.current?.click();
      } else {
        const newImages = [...currentImages, ''];
        setCurrentImages(newImages);
        onChange(newImages);
      }
    }
  };

  const handleFileUpload = (e) => {
    const file = e.target.files?.[0];
    if (file && currentImages.length < maxImages) {
      const reader = new FileReader();
      reader.onloadend = () => {
        const newImages = [...currentImages, reader.result];
        setCurrentImages(newImages);
        onChange(newImages);
      };
      reader.readAsDataURL(file);
    }
    // Reset file input
    if (fileInputRef.current) {
      fileInputRef.current.value = '';
    }
  };

  const handleImageChange = (index, value) => {
    const newImages = [...currentImages];
    newImages[index] = value;
    setCurrentImages(newImages);
    onChange(newImages);
  };

  const handleImageRemove = (index) => {
    const newImages = currentImages.filter((_, i) => i !== index);
    setCurrentImages(newImages);
    onChange(newImages);
  };

  return (
    <div className="space-y-4">
      <div className="flex justify-between items-center">
        <label className="block text-sm font-medium text-gray-700">
          Product Images ({currentImages.length}/{maxImages})
        </label>
        <div className="flex space-x-2">
          <button
            type="button"
            onClick={() => setUploadType('url')}
            className={`p-2 rounded-md ${
              uploadType === 'url' 
                ? 'bg-indigo-100 text-indigo-700' 
                : 'text-gray-700 hover:bg-gray-100'
            }`}
          >
            <LinkIcon className="h-5 w-5" />
          </button>
          <button
            type="button"
            onClick={() => setUploadType('file')}
            className={`p-2 rounded-md ${
              uploadType === 'file' 
                ? 'bg-indigo-100 text-indigo-700' 
                : 'text-gray-700 hover:bg-gray-100'
            }`}
          >
            <PhotoIcon className="h-5 w-5" />
          </button>
        </div>
      </div>

      <div className="space-y-2">
        {currentImages.map((image, index) => (
          <div key={index} className="flex items-center space-x-2">
            <div className="w-12 h-12 flex-shrink-0">
              <img
                src={image}
                alt={`Preview ${index + 1}`}
                className="w-full h-full object-cover rounded-md"
                onError={(e) => {
                  e.target.src = 'https://via.placeholder.com/100?text=Invalid+URL';
                }}
              />
            </div>
            <input
              type="text"
              value={image}
              onChange={(e) => handleImageChange(index, e.target.value)}
              placeholder="Image URL"
              className="flex-1 border border-gray-300 rounded-md shadow-sm py-2 px-3 text-sm focus:ring-indigo-500 focus:border-indigo-500"
            />
            <button
              type="button"
              onClick={() => handleImageRemove(index)}
              className="p-2 text-gray-400 hover:text-gray-500"
            >
              <XMarkIcon className="h-5 w-5" />
            </button>
          </div>
        ))}
      </div>

      {currentImages.length < maxImages && (
        <>
          <input
            type="file"
            ref={fileInputRef}
            onChange={handleFileUpload}
            accept="image/*"
            className="hidden"
          />
          <button
            type="button"
            onClick={handleImageAdd}
            className="w-full py-2 px-3 border border-gray-300 rounded-md shadow-sm text-sm font-medium text-gray-700 hover:bg-gray-50 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-indigo-500 flex items-center justify-center space-x-2"
          >
            {uploadType === 'file' ? (
              <>
                <PhotoIcon className="h-5 w-5" />
                <span>Upload Image</span>
              </>
            ) : (
              <>
                <LinkIcon className="h-5 w-5" />
                <span>Add Image URL</span>
              </>
            )}
          </button>
        </>
      )}
    </div>
  );
}