import { useState } from 'react';
import { useStore } from '../../store';
import { useProductStore } from '../../store/productStore';
import { useThemeStore } from '../../store/themeStore';
import { AccessoryCard } from './AccessoryCard';

export function AccessorySelector({ onNext, onBack }) {
  const { accessories, addAccessory, removeAccessory, updateAccessoryQuantity, recorderType } = useStore();
  const { accessoryData } = useProductStore();
  const { getTheme } = useThemeStore();
  const theme = getTheme();

  // Filter accessories based on recorder type
  const filteredAccessories = accessoryData.filter(accessory => 
    accessory.type === recorderType || accessory.type === 'both'
  );

  const handleAccessorySelect = (accessory) => {
    const isSelected = accessories.some(a => a.id === accessory.id);
    if (isSelected) {
      removeAccessory(accessory.id);
    } else {
      addAccessory(accessory, 1);
    }
  };

  const handleQuantityChange = (accessoryId, quantity) => {
    updateAccessoryQuantity(accessoryId, quantity);
  };

  const getAccessoryQuantity = (accessoryId) => {
    const accessory = accessories.find(a => a.id === accessoryId);
    return accessory ? accessory.quantity : 1;
  };

  return (
    <div className="p-6">
      <h2 className={`text-xl font-semibold text-${theme.text} dark:text-gray-100 mb-6`}>
        Select Accessories
      </h2>
      
      {filteredAccessories.length === 0 ? (
        <div className="text-center py-8">
          <p className={`text-lg text-${theme.text} dark:text-gray-100`}>
            No compatible accessories found for {recorderType} systems.
          </p>
        </div>
      ) : (
        <div className="grid grid-cols-1 gap-6 sm:grid-cols-2 lg:grid-cols-3">
          {filteredAccessories.map((accessory) => (
            <AccessoryCard
              key={accessory.id}
              accessory={accessory}
              isSelected={accessories.some(a => a.id === accessory.id)}
              onSelect={handleAccessorySelect}
              quantity={getAccessoryQuantity(accessory.id)}
              onQuantityChange={(quantity) => handleQuantityChange(accessory.id, quantity)}
            />
          ))}
        </div>
      )}
      
      <div className="mt-8 flex justify-between">
        <button
          onClick={onBack}
          className={`px-4 py-2 border rounded-md hover:bg-${theme.background} dark:hover:bg-gray-700 text-${theme.text} dark:text-gray-100`}
        >
          Back
        </button>
        <button
          onClick={onNext}
          className={`px-4 py-2 bg-${theme.primary}-600 text-white rounded-md hover:bg-${theme.primary}-700`}
        >
          Review Bundle
        </button>
      </div>
    </div>
  );
}