import { useState } from 'react';
import { ImageUploadField } from './ImageUploadField';

export function AddProductForm({ category = 'nvr', onAdd }) {
  const [formData, setFormData] = useState({
    name: '',
    sku: '',
    price: '',
    type: category === 'nvr' ? 'digital' : (category === 'camera' ? 'digital' : 'both'),
    images: [],
    hasColors: category === 'camera' ? true : false,
    ...(category === 'camera' && {
      resolution: '',
      images: {
        black: [],
        white: []
      }
    }),
    ...(category === 'nvr' && {
      channels: ''
    }),
    ...(category === 'accessory' && {
      length: '',
      capacity: '',
      ports: ''
    })
  });

  const handleChange = (e) => {
    const { name, value, type, checked } = e.target;
    setFormData(prev => ({
      ...prev,
      [name]: type === 'checkbox' ? checked : (name === 'price' || name === 'channels' ? parseFloat(value) : value)
    }));
  };

  const handleImageChange = (images) => {
    if (category === 'camera' && formData.hasColors) {
      setFormData(prev => ({
        ...prev,
        images: {
          ...prev.images,
          [prev.currentColor || 'black']: images
        }
      }));
    } else {
      setFormData(prev => ({
        ...prev,
        images
      }));
    }
  };

  const handleColorChange = (color) => {
    setFormData(prev => ({
      ...prev,
      currentColor: color
    }));
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    const newProduct = {
      ...formData,
      id: Date.now(),
      price: parseFloat(formData.price)
    };
    onAdd(category, newProduct);
    setFormData({
      name: '',
      sku: '',
      price: '',
      type: category === 'nvr' ? 'digital' : (category === 'camera' ? 'digital' : 'both'),
      hasColors: category === 'camera' ? true : false,
      images: category === 'camera' ? { black: [], white: [] } : []
    });
  };

  return (
    <form onSubmit={handleSubmit} className="space-y-4 bg-white p-6 rounded-lg shadow">
      <h3 className="text-lg font-medium text-gray-900">Add New {category ? (category.charAt(0).toUpperCase() + category.slice(1)) : 'Product'}</h3>
      
      <div>
        <label className="block text-sm font-medium text-gray-700">Name</label>
        <input
          type="text"
          name="name"
          value={formData.name}
          onChange={handleChange}
          required
          className="mt-1 block w-full border border-gray-300 rounded-md shadow-sm py-2 px-3 focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm"
        />
      </div>

      <div>
        <label className="block text-sm font-medium text-gray-700">SKU</label>
        <input
          type="text"
          name="sku"
          value={formData.sku}
          onChange={handleChange}
          required
          className="mt-1 block w-full border border-gray-300 rounded-md shadow-sm py-2 px-3 focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm"
        />
      </div>

      <div>
        <label className="block text-sm font-medium text-gray-700">Price</label>
        <input
          type="number"
          name="price"
          value={formData.price}
          onChange={handleChange}
          required
          step="0.01"
          className="mt-1 block w-full border border-gray-300 rounded-md shadow-sm py-2 px-3 focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm"
        />
      </div>

      <div>
        <label className="block text-sm font-medium text-gray-700">Type</label>
        <select
          name="type"
          value={formData.type}
          onChange={handleChange}
          required
          className="mt-1 block w-full border border-gray-300 rounded-md shadow-sm py-2 px-3 focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm"
        >
          {category === 'nvr' ? (
            <>
              <option value="digital">Digital (NVR)</option>
              <option value="analog">Analog (XVR)</option>
            </>
          ) : category === 'camera' ? (
            <>
              <option value="digital">Digital (IP Camera)</option>
              <option value="analog">Analog Camera</option>
            </>
          ) : (
            <>
              <option value="digital">Digital Only</option>
              <option value="analog">Analog Only</option>
              <option value="both">Compatible with Both</option>
            </>
          )}
        </select>
      </div>

      {category === 'nvr' && (
        <div>
          <label className="block text-sm font-medium text-gray-700">Channels</label>
          <input
            type="number"
            name="channels"
            value={formData.channels}
            onChange={handleChange}
            required
            className="mt-1 block w-full border border-gray-300 rounded-md shadow-sm py-2 px-3 focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm"
          />
        </div>
      )}

      {category === 'camera' && (
        <>
          <div>
            <label className="block text-sm font-medium text-gray-700">Resolution</label>
            <input
              type="text"
              name="resolution"
              value={formData.resolution}
              onChange={handleChange}
              required
              className="mt-1 block w-full border border-gray-300 rounded-md shadow-sm py-2 px-3 focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm"
            />
          </div>
          <div className="flex items-center">
            <input
              type="checkbox"
              name="hasColors"
              checked={formData.hasColors}
              onChange={handleChange}
              className="h-4 w-4 text-indigo-600 focus:ring-indigo-500 border-gray-300 rounded"
            />
            <label className="ml-2 block text-sm text-gray-900">
              Has multiple colors
            </label>
          </div>
        </>
      )}

      {category === 'camera' && formData.hasColors ? (
        <div className="space-y-4">
          <div>
            <label className="block text-sm font-medium text-gray-700 mb-2">Color</label>
            <div className="flex space-x-4">
              <button
                type="button"
                onClick={() => handleColorChange('black')}
                className={`px-4 py-2 rounded-md ${
                  formData.currentColor === 'black' 
                    ? 'bg-gray-900 text-white' 
                    : 'bg-gray-100 text-gray-900'
                }`}
              >
                Black
              </button>
              <button
                type="button"
                onClick={() => handleColorChange('white')}
                className={`px-4 py-2 rounded-md ${
                  formData.currentColor === 'white' 
                    ? 'bg-gray-100 text-gray-900 border-2 border-gray-900' 
                    : 'bg-gray-100 text-gray-900'
                }`}
              >
                White
              </button>
            </div>
          </div>
          <ImageUploadField 
            images={formData.images[formData.currentColor || 'black']} 
            onChange={handleImageChange}
          />
        </div>
      ) : (
        <ImageUploadField 
          images={formData.images} 
          onChange={handleImageChange}
        />
      )}

      <div>
        <button
          type="submit"
          className="w-full flex justify-center py-2 px-4 border border-transparent rounded-md shadow-sm text-sm font-medium text-white bg-indigo-600 hover:bg-indigo-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-indigo-500"
        >
          Add Product
        </button>
      </div>
    </form>
  );
}