import { useProductStore } from '../store/productStore';

export function getCompatibleCameras(nvrSku) {
  const { cameraData } = useProductStore.getState();
  const nvrCompatibilityMap = {
    // Digital NVRs
    'NVR-PRO-8': ['CAM-HD-1', 'CAM-4K-1', 'CAM-PTZ-1'],
    'NVR-PRO-16': ['CAM-HD-1', 'CAM-4K-1', 'CAM-PTZ-1', 'CAM-PTZ-2'],
    'NVR-ELITE-32': ['CAM-4K-1', 'CAM-4K-2', 'CAM-PTZ-1', 'CAM-PTZ-2'],
    
    // Analog XVRs
    'XVR-BASIC-4': ['CAM-ANALOG-1', 'CAM-ANALOG-2'],
    'XVR-STD-8': ['CAM-ANALOG-1', 'CAM-ANALOG-2', 'CAM-ANALOG-3'],
    'XVR-PRO-16': ['CAM-ANALOG-1', 'CAM-ANALOG-2', 'CAM-ANALOG-3', 'CAM-ANALOG-PTZ']
  };

  // If we have compatibility info for this NVR, return it
  if (nvrSku in nvrCompatibilityMap) {
    return nvrCompatibilityMap[nvrSku];
  }
  
  // If no specific compatibility info, return all camera SKUs of the appropriate type
  // We'll determine the type based on the NVR SKU prefix
  if (nvrSku.startsWith('NVR')) {
    return cameraData
      .filter(camera => camera.type === 'digital')
      .map(camera => camera.sku);
  } else if (nvrSku.startsWith('XVR')) {
    return cameraData
      .filter(camera => camera.type === 'analog')
      .map(camera => camera.sku);
  }
  
  // Fallback to empty array if we can't determine compatibility
  return [];
}

export function calculateBundlePrice(nvrs, cameras, accessories) {
  const { hddOptions } = useProductStore.getState();
  
  const getHddPrice = (hddSize) => {
    const option = hddOptions.find(hdd => hdd.size === hddSize);
    return option ? option.price : 0;
  };

  const nvrPrice = nvrs.reduce((total, nvr) => {
    const hddPrice = getHddPrice(nvr.hddSize || 0);
    return total + ((nvr.price + hddPrice) * (nvr.quantity || 1));
  }, 0);

  const camerasPrice = cameras.reduce((total, camera) => 
    total + (camera.price * (camera.quantity || 1)), 0);

  const accessoriesPrice = accessories.reduce((total, acc) => 
    total + ((acc.price || 0) * (acc.quantity || 1)), 0);
  
  return nvrPrice + camerasPrice + accessoriesPrice;
}