import create from 'zustand';
import { persist } from 'zustand/middleware';

const themes = {
  light: {
    primary: 'indigo',
    background: 'gray-50',
    text: 'gray-900',
    card: 'white',
  },
  dark: {
    primary: 'indigo',
    background: 'gray-900',
    text: 'gray-100',
    card: 'white',
  },
  forest: {
    primary: 'emerald',
    background: 'gray-50',
    text: 'gray-900',
    card: 'white',
  },
  ocean: {
    primary: 'blue',
    background: 'gray-50',
    text: 'gray-900',
    card: 'white',
  },
  sunset: {
    primary: 'rose',
    background: 'gray-50',
    text: 'gray-900',
    card: 'white',
  }
};

export const useThemeStore = create(
  persist(
    (set, get) => ({
      currentTheme: 'light',
      isDark: false,
      setTheme: (theme) => set({ currentTheme: theme }),
      toggleDarkMode: () => set((state) => ({ 
        isDark: !state.isDark,
        currentTheme: state.isDark ? 'light' : 'dark'
      })),
      getTheme: () => themes[get().currentTheme],
    }),
    {
      name: 'theme-storage',
      version: 1,
    }
  )
);