import create from 'zustand';
import { persist } from 'zustand/middleware';

const defaultDiscounts = {
  bulkCamera: { threshold: 4, percentage: 10 },
  accessoryBundle: { threshold: 2, percentage: 15 },
  completeSystem: { threshold: 1, percentage: 5 }
};

export const useDiscountStore = create(
  persist(
    (set) => ({
      discounts: defaultDiscounts,
      updateDiscount: (type, updates) => {
        set((state) => ({
          discounts: {
            ...state.discounts,
            [type]: { ...state.discounts[type], ...updates }
          }
        }));
      },
      resetDiscounts: () => set({ discounts: defaultDiscounts })
    }),
    {
      name: 'discount-storage'
    }
  )
);