import { ImageGallery } from '../common/ImageGallery';
import { useThemeStore } from '../../store/themeStore';

export function CameraCard({ 
  camera, 
  isSelected, 
  selectedColor, 
  quantity, 
  maxQuantity,
  onSelect, 
  onRemove,
  onQuantityChange 
}) {
  const { getTheme } = useThemeStore();
  const theme = getTheme();

  const handleQuantityChange = (e) => {
    e.stopPropagation();
    onQuantityChange(parseInt(e.target.value));
  };

  return (
    <div className={`border rounded-lg p-6 ${isSelected ? 'border-indigo-500' : ''}`}>
      <ImageGallery 
        images={camera.images[selectedColor]} 
        className="mb-4"
      />
      <h3 className={`text-lg font-medium text-${theme.text} dark:text-gray-100`}>{camera.name}</h3>
      <p className="text-sm text-gray-500 dark:text-gray-400">{camera.resolution}</p>
      <p className={`text-sm font-medium text-${theme.primary}-600 dark:text-${theme.primary}-400 mb-4`}>
        ${camera.price}
      </p>

      <div className="flex items-center space-x-4 mb-4">
        <div className="flex-grow">
          <label htmlFor={`quantity-${camera.id}`} className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-1">
            Quantity
          </label>
          <input
            type="range"
            id={`quantity-${camera.id}`}
            min="1"
            max={maxQuantity}
            value={quantity}
            onChange={handleQuantityChange}
            className="w-full h-2 bg-gray-200 rounded-lg appearance-none cursor-pointer dark:bg-gray-700"
          />
        </div>
        <div className="w-12 text-center">
          <span className={`text-lg font-medium text-${theme.text} dark:text-gray-100`}>
            {quantity}
          </span>
        </div>
      </div>

      {isSelected ? (
        <button
          onClick={() => onRemove(camera.id)}
          className={`w-full px-4 py-2 bg-red-600 text-white rounded-md hover:bg-red-700`}
        >
          Remove Camera
        </button>
      ) : (
        <button
          onClick={() => onSelect(camera)}
          disabled={maxQuantity < 1}
          className={`w-full px-4 py-2 bg-${theme.primary}-600 text-white rounded-md hover:bg-${theme.primary}-700 disabled:bg-gray-300 disabled:cursor-not-allowed`}
        >
          Add to Bundle
        </button>
      )}
    </div>
  );
}