import { ChevronLeftIcon, ChevronRightIcon } from '@heroicons/react/24/outline';

export function ImageControls({ onPrevious, onNext, showControls }) {
  if (!showControls) return null;

  return (
    <>
      <div
        onClick={onPrevious}
        className="absolute left-0 top-1/2 transform -translate-y-1/2 bg-black bg-opacity-50 p-1 rounded-r text-white hover:bg-opacity-75 cursor-pointer z-10"
      >
        <ChevronLeftIcon className="h-5 w-5" />
      </div>
      <div
        onClick={onNext}
        className="absolute right-0 top-1/2 transform -translate-y-1/2 bg-black bg-opacity-50 p-1 rounded-l text-white hover:bg-opacity-75 cursor-pointer z-10"
      >
        <ChevronRightIcon className="h-5 w-5" />
      </div>
    </>
  );
}